import React, {Component} from 'react'
import Login from "./Login";
import Register from "./Register";
import {Icon} from 'antd'

class LoginContainer extends Component{
    constructor(props){
        super();
        this.isLogin=props.isLogin;
        this.ving=props.ving;
        this.state={
            login:[]
        };
        this.login=[<Register container={this}/>, <Login container={this}/>];
    }

    componentDidMount(){
        this.setState({
            login:this.login[this.isLogin]
        });
    }

    toReg(){
        let container=this.container;
        container.style.transform='rotateY(90deg)';
        container.style.webkitTransform='rotateY(90deg)';/* Safari 和 Chrome */
        container.style.mozTransform='rotateY(90deg)';/* Firefox */
        let self=this;
        window.setTimeout(()=>{
            self.setState({
                login:self.login[0]
            });
            container.style.transform='rotateY(0)';
            container.style.webkitTransform='rotateY(0)';/* Safari 和 Chrome */
            container.style.mozTransform='rotateY(0)';/* Firefox */
            container.firstChild.style.right='90%';
        },500);
    }

    render(){
        return(
            <div id={'loginContainer'} ref={container=>this.container=container}>
                <Icon type="close" className={'closeLogin'} onClick={()=>{
                    this.container.style.top='-85%';
                    this.container.style.opacity='0';
                    this.setState({
                        login:this.login[this.isLogin]
                    });
                }} />
                {this.state.login}
            </div>
        );
    }
}

export default LoginContainer;


