import React, { Component } from 'react'
import PlayPage from "../pages/PlayPage";

class MoreResult extends Component{
    constructor(props){
        super(props);
        this.type=props.type;
        this.data=props.data;
        this.ving=props.ving;
    }

    componentDidMount(){
        let type=['用户','作品','作品集'];
        this.lead.innerHTML='| '+type[this.type];
        let container=document.getElementById('moreResult');
        let len=this.data.length;
        if (this.type!==0) {
            for (let i = 0; i < len; i++) {
                let div = document.createElement("div");
                div.className = 'one';
                let img = document.createElement("img");
                img.className = 'cover';
                img.alt = '';
                img.src = this.data[i].cover;
                div.appendChild(img);
                let span = document.createElement("span");
                span.className = 'name';
                span.innerHTML = this.data[i].name.length <= 10 ? this.data[i].name : this.data[i].name.substring(0, 10) + '...';
                div.appendChild(span);
                if (this.type === 1) {
                    let self = this;
                    div.onclick = () => {
                        let xmlHttp = new XMLHttpRequest();
                        xmlHttp.open('get', self.ving.url.findProductById + self.data[i].id);
                        xmlHttp.send();
                        xmlHttp.onreadystatechange = () => {
                            if (xmlHttp.readyState !== 4) {}
                            else if (xmlHttp.status !== 200)
                                self.ving.message('发生了未知错误┭┮﹏┭┮' + xmlHttp.status);
                            else {
                                self.ving.setState({
                                    nowPage: null,
                                    moreResult:[]
                                });
                                window.setTimeout(() => {
                                    let product=JSON.parse(xmlHttp.responseText);
                                    self.ving.setState({
                                        nowPage: <PlayPage ving={self.ving} product={product}/>,
                                    });
                                }, 100);
                            }
                        };
                    };
                }
                else {
                    let ving=this.ving;
                    div.onclick=()=>{
                        ving.message('暂不支持跳转到作品集┭┮﹏┭┮');
                    }
                }
                container.appendChild(div);
            }
        }else{
            for (let i=0;i<len;i++){
                let div=document.createElement("div");
                div.className='uOne';
                let img=document.createElement("img");
                img.className='uCover';
                img.alt='';
                img.src=this.data[i].cover;
                div.appendChild(img);
                div.appendChild(document.createElement("br"));
                let span=document.createElement("span");
                span.className='uName';
                span.innerHTML=this.data[i].name.length<=10?this.data[i].name:this.data[i].name.substring(0,10)+'...';
                div.appendChild(span);
                container.appendChild(div);
            }
        }

        let isIE = navigator.userAgent.match(/MSIE (\d)/i);
        isIE = isIE ? isIE[1] : undefined;
        let isFF = /FireFox/i.test(navigator.userAgent);
        if (isIE < 9) //传统浏览器使用MouseWheel事件
            container.attachEvent("onmousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格3行，每行40像素，所以除以120
                container.scrollTop += e.wheelDelta / 2;
                //阻止浏览器默认方法
                return false;
            });
        else if (!isFF) //除火狐外的现代浏览器也使用MouseWheel事件
            container.addEventListener("mousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                container.scrollTop += -e.wheelDelta / 2;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        else //奇葩的火狐使用DOMMouseScroll事件
            container.addEventListener("DOMMouseScroll", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格是3行，但是要注意，这里和像素不同的是它是负值
                container.scrollTop += e.detail * 80;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
    }

    render(){
        return(
            <div id={'moreResult'}>
                <span className={'lead'}>
                  <span className={'content'} ref={lead=>this.lead=lead}> </span>
              </span>
            </div>
        );
    }
}

export default MoreResult;


