import React, {Component} from 'react';
import './Ving.css'
import MainV2 from "./pages/MainV2";
import {Avatar, Icon} from 'antd'
import InitResult from "./components/InitResult";
import Create from "./pages/Create";
import LoginContainer from "./components/LoginContainer";
import PlayPage from "./pages/PlayPage";
import PCenterV2 from "./pages/PCenterV2";
import CreateC from "./pages/CreateC";
import OneCircle from "./pages/OneCircle";
import avatar from './images/avatar.jpg'

class Ving extends Component {
    constructor() {
        super();
        Number.parseFloat=Number.parseFloat!==undefined?Number.parseFloat:(string)=>{
            let l=string.length;
            let number='';
            for (let i=0;i<l;i++){
                if ((string[i]>='a'&&string[i]<='z')||(string[i]>='A'&&string[i]<='Z'))
                    break;
                else {
                    number+=string[i];
                }
            }
            return Number(number);
        };
        Number.parseInt=Number.parseInt!==undefined?Number.parseInt:(string,radix=10)=>{
            let l=string.length;
            let number='';
            for (let i=0;i<l;i++){
                if ((string[i]>='a'&&string[i]<='z')||(string[i]>='A'&&string[i]<='Z')||string[i]==='.')
                    break;
                else {
                    number+=string[i];
                }
            }
            return Number(number);
        };
        Number.find=(duration,position)=>{
            if (duration>240)
                return 2+Math.round(Math.random());
            else if (duration>220)
                return 5;
            else if (duration>180)
                return 0;
            else
                return position;

        };

        this.playPage = null;

        let host = 'http://47.100.111.185:8084';
        //let host='http://localhost:8080';
        this.url = {
            createProductionInfo: host + '/productionCreatePage/createProductionInfo',
            createProductionFile: host + '/productionCreatePage/createProductionFile',
            createAlbumInfo:host+'/collection/createAlbumInfo',
            setAlbumCover:host+'/collection/setAlbumCover',
            mainFindByType: host + '/mainPage/findProductionsByType?type=',
            vingSearch: host + '/mainPage/find?str=',
            login: host + '/mainPage/login?',
            getComment: host + '/productionShowPage/findCommentsByPId?pId=',
            register: host + '/mainPage/createUserInfo',
            getUser: host + '/personalCenter/findUserInfoById?uId=',
            personalCares: host + '/personalCenter/findAttentionEndUserInfoByUID?uId=',
            personalPros: host + '/personalCenter/findAllProductions?uId=',
            personalFans: host + '/personalCenter/findAttentionStartUserInfoByUID?uId=',
            personalCollects: host + '/personalCenter/findAllAlbums?uId=',
            setUserAvatar: host + '/personalCenter/setUserPortrait',
            findProductById: host + '/mainPage/{pId}?pId=',
            addComment: host + '/productionShowPage/addComment',
            updateLoveNumber:host+'/productionShowPage/updateLoveNumber',
            addCommentLoveNumber:host+'/productionShowPage/addCommentLoveNumber',
            addAttentionUser:host+'/personalCenter/addAttentionUser',
            findAttentionEndUserInfoByUID:host+'/personalCenter/findAttentionEndUserInfoByUID?uId=',
            findAttentionStartUserInfoByUID:host+'/personalCenter/findAttentionStartUserInfoByUID?uId=',
            removeAttentionUser:host+'/personalCenter/removeAttentionUser',
            findRecommends:host+'/mainPage/findRecommends',
            setProductionInfo:host+'/personalCenter/setProductionInfo',
            findProductionsByAlbumId:host+'/mainPage/findProductionsByAlbumId?albumId='
        };

        this.state = {
            nowPage: null,
            searchResult: <InitResult ving={this}/>,
            moreResult: [],
            login: null,
            portrait: '',
            tip: {
                content: '',
                opacity: '0',
                zIndex: '0'
            },
            logout: null,

            vingOrReturn: '',
            Ving: '',
            separator: '|',
            first: '',
            second: '',
            third: '',
            fourth: '',
            return: null,
            firstClick: null,
            secondClick: null,
            thirdClick: null,
            fourthClick: null
        };
        this.height = 728;
        this.fSize = 16;
        this.queryResult = '';


        this.stackIndex = [];
        this.stackContents = [<MainV2 ving={this}/>, <Create ving={this}/>, <PCenterV2 ving={this}/>, <CreateC ving={this}/>, <OneCircle ving={this}/>];
        this.stackL = 0;
        this.nowIndex = 0;
        this.user = null;
        this.oneAuthor=null;
        this.moreOrCare='more';
        this.carePeople=null;
        this.mainPage=null;
        this.cSrc=['','','','',''];
        this.cName=['','','','',''];
        this.intoRecommend=[null,null,null,null,null];
        this.collects=[];

        this.someProducts=[];
        this.messageTime=0;
    }

    message(content, time = 2000) {
        this.setState({
            tip: {
                opacity: '1',
                content: content,
                zIndex: '12'
            }
        });
        let self = this;
        window.clearTimeout(this.messageTime);
        this.messageTime=window.setTimeout(() => {
            self.setState({
                tip: {
                    opacity: '0',
                    zIndex: '0'
                }
            });
        }, time);
    }

    setCookie(c_name, value, expirehours) {
        let exdate = new Date();
        exdate.setHours(exdate.getHours() + expirehours);
        document.cookie = c_name + "=" + escape(value) + ((expirehours == null) ? "" : ";expires=" + exdate.toGMTString());
    }

    getCookie(c_name) {
        if (document.cookie.length > 0) {　　//先查询cookie是否为空，为空就return ""
            let c_start = document.cookie.indexOf(c_name + "=");　　//通过String对象的indexOf()来检查这个cookie是否存在，不存在就为 -1　　
            if (c_start !== -1) {
                c_start = c_start + c_name.length + 1;　　//最后这个+1其实就是表示"="号啦，这样就获取到了cookie值的开始位置
                let c_end = document.cookie.indexOf(";", c_start);　　//其实我刚看见indexOf()第二个参数的时候猛然有点晕，后来想起来表示指定的开始索引的位置...这句是为了得到值的结束位置。因为需要考虑是否是最后一项，所以通过";"号是否存在来判断
                if (c_end === -1) c_end = document.cookie.length;
                return unescape(document.cookie.substring(c_start, c_end));　　//通过substring()得到了值。想了解unescape()得先知道escape()是做什么的，都是很重要的基础，想了解的可以搜索下，在文章结尾处也会进行讲解cookie编码细节
            }
        }
        return ""
    }

    findRecommends(uId=''){
        let ving=this;
        let self=this;
        let xmlHttp=new XMLHttpRequest();
        let url=ving.url.findRecommends+(uId===''?'':'?uId='+uId);
        let products=[];
        xmlHttp.open('get',url);
        //xmlHttp.send();
        xmlHttp.onreadystatechange=()=>{
            if (xmlHttp.readyState!==4){}
            else if (xmlHttp.status!==200)
                ving.message('在向您推荐时发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
            else {
                if (xmlHttp.responseText==='')
                    return;
                products=JSON.parse(xmlHttp.responseText);
                let cnt=products.length;
                if (cnt<5){
                    let addXml=new XMLHttpRequest();
                    addXml.open('get',ving.url.findRecommends);
                    addXml.send();
                    addXml.onreadystatechange=()=>{
                        if (addXml.readyState!==4){}
                        else if (addXml.status!==200)
                            ving.message('向您推荐时发生了未知错误┭┮﹏┭┮'+addXml.status);
                        else {
                            if (addXml.responseText===''){}
                            else {
                                let tmp=JSON.parse(addXml.responseText);
                                let l=tmp.length;
                                for (let i=0;i<l;i++){
                                    let flag=0;
                                    for (let j=0;j<cnt;j++){
                                        if (products[j].pId===tmp[i].pId){
                                            flag=1;
                                            break;
                                        }
                                    }
                                    if (!flag){
                                        products[cnt++]=tmp[i];
                                        if (cnt>=5)
                                            break;
                                    }
                                }
                                products.forEach((item,index)=>{
                                    self.cName[index]=item.pName;
                                    self.cSrc[index]=item.cover;
                                    self.intoRecommend[index]=()=>{
                                        ving.setState({
                                            nowPage:<PlayPage ving={ving} product={item} />
                                        });
                                    };
                                });
                                if (self.mainPage!==null){
                                    self.mainPage.setState({
                                        cName:self.cName,
                                        cSrc:self.cSrc,
                                        intoRecommend:self.intoRecommend
                                    });
                                }
                            }
                        }
                    };
                }else {
                    products.forEach((item,index)=>{
                        self.cName[index]=item.pName;
                        self.cSrc[index]=item.cover;
                        self.intoRecommend[index]=()=>{
                            ving.setState({
                                nowPage:<PlayPage ving={ving} product={item} />
                            });
                        };
                    });
                    if (self.mainPage!==null){
                        self.mainPage.setState({
                            cName:self.cName,
                            cSrc:self.cSrc,
                            intoRecommend:self.intoRecommend
                        });
                    }
                }
            }
        };

    }

    login(uId, password) {
        this.user={
            portrait:avatar,
            uName:'小兔子',
            signature:'兔子要吃胡萝卜',
            fansNumber:630,
            concernNumber:1250,
            productionNumber:48,
            albumNumber:10,
            loveNumber:895
        };
        this.setState({
            login:null
        });
        return;

        let self = this;
        let xmlHttp = new XMLHttpRequest();
        xmlHttp.open('get', this.url.login + 'id=' + uId + '&pwd=' + password);
        xmlHttp.send();
        xmlHttp.onreadystatechange = () => {
            if (xmlHttp.readyState !== 4) {
            }
            else if (xmlHttp.status !== 200) {
                self.message('发生了未知错误');
            }
            else {
                let response = xmlHttp.responseText;
                if (response === "") {
                    self.message('未知错误');
                } else if (response === "not exist") {
                    self.message('用户不存在');
                } else if (response === "not match") {
                    self.message('密码不正确');
                } else {
                    let infoXML = new XMLHttpRequest();
                    infoXML.open('get', self.url.getUser + uId);
                    infoXML.send();
                    infoXML.onreadystatechange = () => {
                        if (infoXML.readyState !== 4) {}
                        else if (infoXML.status !== 200)
                            self.message('发生了未知错误┭┮﹏┭┮');
                        else {
                            self.user = JSON.parse(infoXML.responseText);
                            self.findRecommends(self.user.uId);
                            self.setState({
                                portrait: self.user.portrait,
                                login: null,
                                logout: <span className={'logout'} ref={logout => this.logout = logout} onClick={() => {
                                    self.setState({
                                        logout: null,
                                        portrait: '',
                                        login: <LoginContainer isLogin={1} ving={this}/>
                                    });
                                    if (self.state.nowPage===self.stackContents[2])
                                        self.returnMain();
                                    self.user = null;
                                    self.setCookie('uId', '');
                                    self.setCookie('password', '');
                                    self.findRecommends();
                                }} onMouseOver={() => {
                                    this.logout.style.opacity = 1;

                                }} onMouseOut={() => {
                                    this.logout.style.opacity = 0;

                                }}>退出</span>
                            });
                            self.message('登录成功o(*￣▽￣*)o');
                            if (self.playPage !== null) {
                                self.playPage.setState({
                                    uName: self.user.uName,
                                    avatar: self.user.portrait,
                                });
                            }
                            self.findRecommends(uId);
                            this.setCookie('uId', uId, 1);
                            this.setCookie('password', password, 1);
                            //document.cookie = 'uId=' + uId;
                            //document.cookie = 'password=' + password;

                            let careXml=new XMLHttpRequest();
                            careXml.open('get',self.url.findAttentionEndUserInfoByUID+uId);
                            careXml.send();
                            careXml.onreadystatechange=()=>{
                                if (careXml.readyState!==4){}
                                else if (careXml.status!==200)
                                    self.message('发生了一点小小的错误诶');
                                else {
                                    if (careXml.responseText==='')
                                        return;
                                    self.carePeople=JSON.parse(careXml.responseText);
                                }
                            };
                            let collectXml=new XMLHttpRequest();
                            collectXml.open('get',self.url.personalCollects+uId);
                            collectXml.send();
                            collectXml.onreadystatechange=()=>{
                                if (collectXml.readyState!==4){}
                                else if (collectXml.status!==200)
                                    self.message('发生了一点小小的错误诶');
                                else {
                                    if (collectXml.responseText==='')
                                        return;
                                    self.collects=JSON.parse(collectXml.responseText);
                                }
                            };
                        }
                    };
                }
            }

        }
    }

    returnLast() {
        if (this.stackL > 0) {
            this.setState({
                nowPage: this.stackContents[this.stackIndex[--this.stackL]]
            });
            this.setCookie('isP', '');
            this.nowIndex=this.stackIndex[this.stackL];
        }
    }

    toNext(index) {
        /*for (let i = 0; i < this.stackL; i++) {
            if (index === this.stackIndex[i]) {
                this.setState({
                    nowPage: this.stackContents[index]
                });
                this.stackL = i + 1;
                return;
            }
        }*/
        this.setState({
            nowPage: this.stackContents[index]
        });
        this.stackIndex[this.stackL++] = this.nowIndex;
        this.nowIndex = index;
        this.setCookie('isP', '');
    }

    returnMain() {
        this.stackL = 0;
        this.toNext(0);
        this.setCookie('isP', '');
    }

    touchMove(element,dir=0){
        let start;
        function doMove(e){
            e.preventDefault();
            let end=e.touches[0];
            if (dir===0)
                element.scrollTop+=start.clientY-end.clientY;
            else
                element.scrollLeft+=start.clientX-end.clientX;
            start=end;
        }
        element.addEventListener('touchstart', (e)=>{
            start=e.touches[0];
            element.addEventListener('touchmove',(doMove));
        });
        element.addEventListener('touchend',()=>{
            element.removeEventListener('touchmove',doMove);
        });
    }

    componentDidMount() {
        let self = this;

        /*document.body.onselectstart = function () {
            return false;
        };*/
        window.onresize = function () {
            self.fSize = self.fSize * document.body.clientHeight / self.height;
            let html = document.querySelector('html');
            self.height = document.body.clientHeight;
            html.style.fontSize = self.fSize + 'px';
        };
        window.onresize();

        this.input.onfocus = function () {
            let result = document.getElementById('searchResult');
            result.style.opacity = 1;
            result.style.zIndex = 10;

        };
        this.input.onblur = function () {
            let result = document.getElementById('searchResult');
            result.style.opacity = 0;
            result.style.zIndex = 0;
            self.setState({
                moreResult: []
            });
        };

        let uId, password;
        if ((uId = this.getCookie('uId')) !== '' && (password = this.getCookie('password')) !== '') {
            this.login(uId, password);
        }
        else if (this.user === null) {
            this.setState({
                login: <LoginContainer isLogin={1} ving={this}/>
            });
        }

        let pId;
        if (this.getCookie('isP') !== '' && (pId = this.getCookie('product')) !== '') {
            let xmlHttp = new XMLHttpRequest();
            xmlHttp.open('get', this.url.findProductById + pId);
            xmlHttp.crossOrigin = true;
            //xmlHttp.send();
            xmlHttp.onreadystatechange = () => {
                if (xmlHttp.readyState !== 4) {
                }
                else if (xmlHttp.status !== 200)
                    this.message('发生了未知错误┭┮﹏┭┮' + xmlHttp.status);
                else {
                    let product = JSON.parse(xmlHttp.responseText);
                    this.setState({
                        nowPage: <PlayPage ving={this} product={product}/>
                    });
                    this.setCookie('product', pId, 1);
                }
            };
        } else
            this.setState({
                nowPage: <MainV2 ving={this}/>
            });

        this.message('欢迎光临(/≧▽≦)/');
        this.findRecommends(this.user===null?'':this.user.uId);

    }

    render() {
        return (
            <div id={'Ving'} className="Ving">
          <span style={{opacity: this.state.tip.opacity, zIndex: this.state.tip.zIndex}} className={'messageTip'}>
              <span className={'tipContent'}>{this.state.tip.content}</span>
          </span>
                <span className={'topNavigate'} ref={navigate => this.navigate = navigate}>
              <span>
                  <span onClick={this.state.return}>{this.state.vingOrReturn}</span>
                  &nbsp;|&nbsp;
                  <span onClick={this.returnMain.bind(this)}>{this.state.Ving}</span>
              </span>
              <span className={'topIndex'}>
                  <span onClick={this.state.firstClick}>{this.state.first}</span>
                  {this.state.separator}
                  <span onClick={this.state.secondClick}>{this.state.second}</span>
                  {this.state.separator}
                  <span onClick={this.state.thirdClick}>{this.state.third}</span>
                  {this.state.separator}
                  <span onClick={this.state.fourthClick}>{this.state.fourth}</span>
              </span>
          </span>
                <span className={'fixContainer'}>
                      <span className={'topSearch'}>
                          <input ref={input => this.input = input} type={'text'} placeholder={''}
                                 className={'searchInput'}
                                 onChange={(e) => {
                                     let self = this;
                                     let value = e.target.value;
                                     let i;
                                     let len = value.length;
                                     for (i = 0; i < len; i++) {
                                         if (value[i] !== ' ') {
                                             value = value.substring(i);
                                             break;
                                         }
                                     }
                                     if (i === len || value === '')
                                         return;
                                     let xmlHttp = new XMLHttpRequest();
                                     xmlHttp.onreadystatechange = function () {
                                         if (xmlHttp.status !== 200 || xmlHttp.readyState !== 4)
                                             return;
                                         self.queryResult = JSON.parse(xmlHttp.responseText);
                                         self.setState({
                                             searchResult: <InitResult ving={self}/>,
                                             moreResult: []
                                         });
                                     };
                                     xmlHttp.open('GET', self.url.vingSearch + value, true);
                                     //xmlHttp.send();
                                 }}/>
                          <div className={'searchButton'}><Icon type="search"/></div>
                      </span>
                      <Avatar src={this.state.portrait} className={'topAvatar'} onClick={() => {
                          if (this.user === null) {
                              let container = document.getElementById('loginContainer');
                              if (container===null)
                                  return;
                              container.style.top = '7%';
                              container.style.opacity = '1';
                              this.message('就是让您看看这个界面，点一下登录就能进去啦');
                          } else {
                              this.moreOrCare='more';
                              this.setState({
                                  nowPage:null
                              });
                              let self=this;
                              window.setTimeout(()=>{
                                  self.toNext(2);
                              },1)
                          }
                      }}/>
                    {this.state.logout}
                    <span onClick={() => {
                        /*if (this.user !== null)
                            this.toNext(1);
                        else
                            this.message('请先点击搜索框右边那个圆圆的小东西登录噢~o(*￣▽￣*)o', 4000);*/
                        this.toNext(1);
                    }} className={'publish'}>投稿</span>
                    <span onClick={()=>{
                        if (this.user!==null)
                            this.toNext(3);
                        else
                            this.message('请先点击搜索框右边那个圆圆的小东西登录噢~o(*￣▽￣*)o', 4000);
                    }} className={'collectPub'}>收录</span>
          </span>
                {this.state.login}

                {this.state.searchResult}
                {this.state.moreResult}


                {this.state.nowPage}
            </div>
        );
    }
}

export default Ving;



