import React, { Component} from 'react'
import {Icon} from 'antd'
import '../css/Create.css'
import UpLoadAndRecord from "../components/UpLoadAndRecord";

class Create extends Component{
    constructor(props){
        super(props);
        this.ving=props.ving;
        this.state={
            creation:<UpLoadAndRecord create={this}/>
        };
        this.circle=[];
        this.music='';
    }

    componentDidMount(){
        let ving=this.ving;
        let navigate=ving.navigate;
        navigate.style.left='5%';

        ving.setState({
            vingOrReturn:'< 返回',
            Ving:'微音',
            separator:'',
            first:'',
            second:'',
            third:'',
            fourth:'',
            return:()=>{
                ving.returnLast();
            },
        });

        this.last=0;
        let self=this;
        for (let i=0;i<5;i++) {
            this.circle[i].onclick=function () {
                self.circle[self.last].style.color='#70e8f5';
                self.circle[self.last].style.backgroundColor='#343a40';
                this.style.color='#343a40';
                this.style.backgroundColor='#70e8f5';
                self.last=i;
                ving.setCookie('draftCircle',self.last+'',240);
            }
        }

        let draftCircle=ving.getCookie('draftCircle');
        if (draftCircle!==''){
            this.circle[Number.parseInt(draftCircle,10)].click();
        }else
            this.circle[0].click();
        if (ving.getCookie('draftVisible')==='1')
            this.select.click();
        this.title.value=ving.getCookie('draftTitle');
        this.label.value=ving.getCookie('draftLabel');
        this.description.value=ving.getCookie('draftDes');
    }

    render(){
        return(
            <div id={'create'}>
                <div className={'left'}>
                    <div className={'cover'}>
                        <span className={'character'}>+</span>
                        <p className={'coverTip'}>点击这里上传封面</p>
                        <input ref={cover=>this.cover=cover} type={'file'} className={'coverFile'} onChange={(e)=>{
                            let div=this.cover.parentNode;
                            let span=div.getElementsByClassName('character')[0];
                            let p=div.getElementsByClassName('coverTip')[0];
                            let image=div.getElementsByClassName('coverImg')[0];
                            span.style.opacity=0;
                            p.style.opacity=0;
                            if (image!==undefined){
                                image.src=URL.createObjectURL(this.cover.files[0]);
                            }else {
                                image=new Image();
                                image.src=URL.createObjectURL(this.cover.files[0]);
                                image.alt='无法显示哦，请检查是否上传了错误的文件';
                                image.className='coverImg';
                                div.appendChild(image);
                            }
                            div.style.border='0px';
                        }}/>
                    </div>
                    <div ref={life=>this.circle[0]=life} className={'life'}>生活圈</div>
                    <div ref={dubing=>this.circle[3]=dubing} className={'dubbing'}>配音圈</div><br/>
                    <div ref={reading=>this.circle[2]=reading} className={'reading'}>朗读圈</div>
                    <div ref={ASMR=>this.circle[4]=ASMR} className={'ASMR'}>ASMR圈</div><br/>
                    <div ref={oralSkill=>this.circle[1]=oralSkill} className={'oralSkill'}>口技圈</div><br/>
                    <span className={'visibilityLead'}>对谁可见</span><br/>
                    <div className={'visibility'} onMouseOver={
                        ()=>{
                            this.select.style.opacity=1;
                        }
                    } onMouseOut={
                        ()=>{
                            this.select.style.opacity=0;
                        }
                    }>所有人<Icon type="down" className={'down'}/></div>
                    <span className={'selection'} ref={select=>this.select=select} onMouseOver={
                        ()=>{
                            this.select.style.opacity=1;
                            this.select.style.backgroundColor='grey';
                        }
                    } onMouseOut={
                        ()=>{
                            this.select.style.backgroundColor='#343a40';
                            this.select.style.opacity=0;
                        }
                    } onClick={
                        ()=>{
                            let last=this.select.innerText;
                            let icon=this.select.previousSibling.getElementsByClassName("down")[0];
                            this.select.innerHTML=this.select.previousSibling.innerText;
                            this.select.previousSibling.innerText=last;
                            this.select.previousSibling.appendChild(icon);

                            this.ving.setCookie('draftVisible',last==='所有人'?'0':'1',240);
                        }
                    }>仅自己</span>
                </div>


                <div className={'right'}>
                    {this.state.creation}
                    <div className={'otherInfo'}>
                        <span className={'titleLead'}>作品名称：</span>
                        <input ref={title=>this.title=title} type={'text'} className={'title'} onChange={(e)=>{
                            this.ving.setCookie('draftTitle',e.target.value,240);
                        }}/>
                        <br/>
                        <span className={'labelLead'}>标签：</span>
                        <input ref={label=>this.label=label} type={'text'} className={'label'} placeholder={'请用";"分隔每一个标签'} onChange={(e)=>{
                            this.ving.setCookie('draftLabel',e.target.value,240);
                        }}/>
                        <br/>
                        <span className={'descriptionLead'}>简介：</span>
                        <br/>
                        <textarea ref={description=>this.description=description} className={'description'} maxLength={'100'} placeholder={'这个人什么都没写'} onChange={(e)=>{
                            this.ving.setCookie('draftDes',e.target.value,240);
                        }}/>
                        <button className={'finish'} onClick={()=>{
                            let info={
                                type:this.last,
                                visible:this.select.innerText==='所有人'?1+'':0+'',
                                pName:this.title.value,
                                label:this.label.value,
                                description:this.description.value,
                                authorId:this.ving.user.uId
                            };

                            let self=this;

                            self.ving.message('正在上传文件中...请耐心等待页面跳转o(*￣▽￣*)o',5000);
                            let infoXml=new XMLHttpRequest();
                            infoXml.open('post',this.ving.url.createProductionInfo,true);
                            infoXml.setRequestHeader('Content-Type', 'application/json');
                            infoXml.send(JSON.stringify(info));
                            infoXml.onreadystatechange=()=>{
                                if (infoXml.readyState!==4){}
                                else if (infoXml.status!==200)
                                    self.ving.message('发生了未知错误┭┮﹏┭┮'+infoXml.status);
                                else {
                                    let files=new FormData();
                                    let music=this.music;
                                    files.append('pId',infoXml.responseText);
                                    files.append('file',music);
                                    files.append('cover',this.cover.files[0]);
                                    files.append('start',music.size*self.start+'');
                                    files.append('end',music.size*self.end+'');

                                    let fileXml=new XMLHttpRequest();
                                    fileXml.open('post',self.ving.url.createProductionFile,true);
                                    fileXml.send(files);
                                    fileXml.onreadystatechange=()=>{
                                        if (fileXml.readyState!==4){}
                                        else if (fileXml.status!==200)
                                            self.ving.message('发生了未知错误┭┮﹏┭┮'+fileXml.status);
                                        else{
                                            self.ving.toNext(0);
                                            self.ving.user.productionNumber++;
                                        }
                                    }
                                }
                            };
                        }}>完<br/>成</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Create;


