import React, {Component} from 'react'
import avatar from '../images/avatar.jpg'
import MoreResult from "./MoreResult";
import PlayPage from "../pages/PlayPage";

class InitResult extends Component{
    constructor(props){
        super(props);
        this.contents=[];
        this.props=props;
        this.queryResult='';
        this.more=[];
    }

    componentDidMount(){
        for (let i=0;i<3;i++) {
            let element = this.contents[i];
            element.firstChild.src = '';
            element.lastChild.innerHTML = '';
            element = element.nextSibling;
            element.firstChild.src = '';
            element.lastChild.innerHTML = '';
        }

        let ving=this.props.ving;
        let more=this.more;
        //let type=['用户','作品','作品集'];
        for (let i=0;i<3;i++){
            more[i].onclick=function () {
                let result=ving.queryResult;
                let qLen=result.length;
                let data=[];
                let cnt=0;
                for (let j=0;j<qLen;j++){
                    if (Number.parseInt(result[j].type,10)===i+1)
                        data[cnt++]=result[j];
                }
                ving.setState({
                    moreResult:<MoreResult type={i} data={data} ving={ving}/>
                });
                document.getElementById('searchResult').style.opacity=0;
            }
        }
    }

    componentWillReceiveProps(props){
        this.componentDidMount();

        let result=props.ving.queryResult;
        let cnt=[0,0,0];
        let len=result.length;
        //1用户，2作品，3作品集XMLHttpRequest
        for (let i=0;i<len;i++){
            let index=Number.parseInt(result[i].type,10)-1;
            if(cnt[index]===2)
                continue;
            let element=this.contents[index];
            element.firstChild.src=result[i].cover;
            element.lastChild.innerHTML=result[i].name;
            if (index===1){
                element.onclick=()=>{
                    let xmlHttp=new XMLHttpRequest();
                    xmlHttp.open('get',props.ving.url.findProductById+result[i].id);
                    xmlHttp.send();
                    xmlHttp.onreadystatechange=()=>{
                        if (xmlHttp.readyState !== 4){}
                        else if (xmlHttp.status!==200)
                            props.ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                        else {
                            props.ving.setState({
                                nowPage:null
                            });
                            window.setTimeout(()=>{
                                let product=JSON.parse(xmlHttp.responseText);
                                props.ving.setState({
                                    nowPage:<PlayPage ving={props.ving} product={product}/>
                                });
                            },1);
                        }
                    };
                };
            }
            else if (index===0){
                element.onclick=()=>{
                    let xmlHttp=new XMLHttpRequest();
                    xmlHttp.open('get',props.ving.url.getUser+result[i].id);
                    xmlHttp.send();
                    xmlHttp.onreadystatechange=()=>{
                        if (xmlHttp.readyState !== 4){}
                        else if (xmlHttp.status!==200)
                            props.ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                        else {
                            props.ving.setState({
                                nowPage:null
                            });
                            props.ving.oneAuthor=JSON.parse(xmlHttp.responseText);
                            props.ving.moreOrCare='care';
                            window.setTimeout(()=>{
                                props.ving.toNext(2);
                            },1);
                        }
                    };
                };
            }
            else {
                element.onclick=()=>{
                    props.ving.message('暂不支持跳转到作品集┭┮﹏┭┮');
                }
            }
            this.contents[index]=this.contents[index].nextSibling;
            cnt[index]++;
        }
    }

    render(){
        return(
            <div id={'searchResult'}>
              <span className={'lead'}>
                  <span className={'content'}>|&nbsp;作品</span>
                  <span className={'more'}  ref={product=>this.more[1]=product}>更多</span>
              </span>
                <div className={'one'} ref={product=>this.contents[1]=product}>
                    <img className={'cover'} src={avatar} alt={''}/>
                    <span className={'name'}>下午好啊单身狗</span>
                </div>
                <div className={'one'}>
                    <img className={'cover'} src={avatar} alt={''}/>
                    <span className={'name'}>下午好啊单身狗</span>
                </div>

                <span className={'lead'}>
                  <span className={'content'}>|&nbsp;作品集</span>
                  <span className={'cMore'} ref={collection=>this.more[2]=collection}>更多</span>
                </span>
                <div className={'one'} ref={collection=>this.contents[2]=collection}>
                    <img className={'cover'} src={avatar} alt={''}/>
                    <span className={'name'}>下午好啊单身狗</span>
                </div>
                <div className={'one'}>
                    <img className={'cover'} src={avatar} alt={''}/>
                    <span className={'name'}>下午好啊单身狗</span>
                </div>

                <span className={'lead'}>
                  <span className={'content'}>|&nbsp;用户</span>
                  <span className={'more'} ref={user=>this.more[0]=user}>更多</span>
                </span>
                <br/>
                <div className={'uOne'} ref={user=>this.contents[0]=user}>
                    <img className={'uCover'} src={avatar} alt={''}/>
                    <br/>
                    <span className={'uName'}>大狗子</span>
                </div>
                <div className={'uOne'}>
                    <img className={'uCover'} src={avatar} alt={''}/>
                    <br/>
                    <span className={'uName'}>二狗子</span>
                </div>
            </div>
        );
    }
}

export default InitResult;


