import React, {Component} from 'react'

class Login extends Component{
    constructor(props){
        super();
        this.container=props.container;
    }

    render(){
        return(
            <div id={'login'}>
                <span className={'lead'}>用户登录</span>
                <p className={'accountLead'}>账号：</p>
                <input type={'email'} className={'account'} ref={'account'}/>
                <p className={'passwordLead'}>密码：</p>
                <input type={'password'} className={'password'} ref={'password'} minLength={'6'} maxLength={'20'} onFocus={()=>{
                    let self=this;
                    let cnt=0;
                    document.onkeyup=(e)=>{
                        if (e.keyCode===13&&cnt===0){
                            cnt++;
                            self.container.ving.login(self.refs.account.value,self.refs.password.value);
                        }
                    }
                }} onBlur={()=>{
                    document.onkeyup=null;
                }}/>
                <button className={'login'} onClick={()=>{
                    this.container.ving.login(this.refs.account.value,this.refs.password.value);
                }}>登录</button>
                <button className={'register'} onClick={()=>{
                    this.container.toReg();
                }}>注册</button>
            </div>
        );
    }
}

export default Login;


