import React, {Component} from 'react'

class Flower extends Component{
    constructor(props){
        super(props);
        this.className=' '+props.className;
        this.container=props.container;
    }

    componentDidMount(){
        let canvas = document.getElementById('canvas'),
            canvasCtx = canvas.getContext("2d"),
            canvasWidth=canvas.width,
            canvasHeight=canvas.height;
        canvas.style.width='100%';


        let analyser = this.container.analyser;  // 提取音频数据，你需要AnalyserNode，它可以通过createAnalyser创建
        if (analyser===null)
            return;

        analyser.fftSize=256;
        let planets = new Uint8Array(analyser.frequencyBinCount);

        let requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame;

        Array.max = function( array ){
            return Math.max.apply( Math, array );
        };
        let history=[];
        let hisLength=120;
        for(let i=0;i<hisLength;i++){
            history[i]={
                x:0,
                y:0,
                r:0,
                vx:0,
                vy:0,
                vr:0,
                red:0,
                green:0,
                blue:0
            };
        }

        let startPosition=0;
        let nowPosition=0;

        let self=this;
        function draw() {
            analyser.getByteFrequencyData(planets);
            canvasCtx.fillStyle = '#343a40';
            canvasCtx.fillRect(0, 0, canvasWidth, canvasHeight);

            for(nowPosition=(startPosition+1)%hisLength;startPosition!==nowPosition;nowPosition=(nowPosition+1)%hisLength){
                if (history[nowPosition].vr===0)
                    continue;
                canvasCtx.beginPath();
                canvasCtx.arc(history[nowPosition].x,history[nowPosition].y,history[nowPosition].r,0,2*Math.PI);
                canvasCtx.fillStyle = 'rgb('+history[nowPosition].red+', '+history[nowPosition].green+', '+history[nowPosition].blue+')';
                canvasCtx.fill();
                history[nowPosition].x+=history[nowPosition].vx;
                history[nowPosition].y+=history[nowPosition].vy;
                history[nowPosition].r+=history[nowPosition].vr;
            }

            if (Array.max(Array.from(planets))>240){
                let x,y,r;
                x=canvasWidth/2;
                y=canvasHeight/3;
                r=10*Math.random();
                canvasCtx.beginPath();
                canvasCtx.arc(x,y,r,0,2*Math.PI);
                let red=255*Math.random(),
                    green=255*Math.random(),
                    blue=255*Math.random();
                canvasCtx.fillStyle = 'rgb('+red+', '+green+', '+blue+')';
                canvasCtx.fill();
                history[startPosition].x=x;
                history[startPosition].y=y;
                history[startPosition].r=r;
                history[startPosition].vx=16*Math.random()-8;
                history[startPosition].vy=8*Math.random()-4;
                history[startPosition].vr=0.05+0.25*Math.random();
                history[startPosition].red=red;
                history[startPosition].green=green;
                history[startPosition].blue=blue;
            }
            else
                history[startPosition].vr=0;
            startPosition=(startPosition+1)%hisLength;

            self.drawTime=requestAnimationFrame(draw);
        }

        draw();
    }

    componentWillUnmount(){
        cancelAnimationFrame(this.drawTime);
    }


    render(){
        return(
            <div className={'star'+this.className}>
                <canvas id="canvas" width={this.container.width} height={this.container.height}/>
            </div>
        );
    }
}

export default Flower;


