import React, {Component} from 'react'
import '../css/PCenterV2.css'
import like from '../images/like.png'
import comment from '../images/comment.png'
import add from '../images/add.png'
import {Avatar} from 'antd'
import PlayPage from "./PlayPage";

class PCenterV2 extends Component{
    constructor(props){
        super();
        this.state={
            avatar:'',
            name:'',
            sign:'',
            fans:0,
            care:0,
            proNumber:0,
            colNumber:0,
            likeNumber:0,

            myCares:null,

            moreOrCare:null,
            careLWord:'关注列表'
        };
        this.ving=props.ving;
        this.moreOrCare=null;
        if (this.ving.moreOrCare==='more')
            this.user=this.ving.user;
        if (this.ving.moreOrCare==='care')
            this.user=this.ving.oneAuthor;
    }

    componentDidMount(){
        this.pLead.style.borderLeft= '0.1rem solid #70e8f5';

        let ving=this.ving,
            product=this.product,
            collect=this.collect;
        ving.setState({
            vingOrReturn:'< 返回',
            Ving:'微音',
            separator:'',
            first:'',
            second:'',
            third:'',
            fourth:'',
            return:()=>{
                ving.returnLast();
            },
            firstClick:null,
            secondClick:null,
            thirdClick:null,
            fourthClick:null
        });
        let isIE = navigator.userAgent.match(/MSIE (\d)/i);
        isIE = isIE ? isIE[1] : undefined;
        let isFF = /FireFox/i.test(navigator.userAgent);
        if (isIE < 9) //传统浏览器使用MouseWheel事件
        {
            product.attachEvent("onmousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格3行，每行40像素，所以除以120
                product.scrollTop += e.wheelDelta / 4;
                //阻止浏览器默认方法
                return false;
            });
            collect.attachEvent("onmousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格3行，每行40像素，所以除以120
                collect.scrollTop += e.wheelDelta / 4;
                //阻止浏览器默认方法
                return false;
            });
        }
        else if (!isFF) //除火狐外的现代浏览器也使用MouseWheel事件
        {
            product.addEventListener("mousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                product.scrollTop += -e.wheelDelta / 4;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
            collect.addEventListener("mousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                collect.scrollTop += -e.wheelDelta / 4;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        }
        else //奇葩的火狐使用DOMMouseScroll事件
        {
            product.addEventListener("DOMMouseScroll", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格是3行，但是要注意，这里和像素不同的是它是负值
                product.scrollTop += e.detail * 80;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
            collect.addEventListener("DOMMouseScroll", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格是3行，但是要注意，这里和像素不同的是它是负值
                collect.scrollTop += e.detail * 80;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        }
        ving.touchMove(product);
        ving.touchMove(collect);

        this.setState({
            avatar:this.user.portrait,
            name:this.user.uName,
            sign:this.user.signature,
            fans:this.user.fansNumber,
            care:this.user.concernNumber,
            proNumber:this.user.productionNumber,
            colNumber:this.user.albumNumber,
            likeNumber:this.user.loveNumber
        });
        if (ving.moreOrCare==='more'){
            this.setState({
                moreOrCare:<span className={'more'} onClick={()=>{
                    ving.message('┭┮﹏┭┮');
                }}>更多资料</span>
            });
        }
        else if (ving.moreOrCare==='care'){
            this.setState({
                moreOrCare:<span ref={moreOrCare=>this.moreOrCare=moreOrCare} className={'doCare'} onClick={()=>{
                    if (ving.user===null){
                        this.ving.message('登录之后才能关注哦，不然对方怎么知道自己的粉丝是谁呢(￣▽￣)"');
                        return;
                    }
                    let l=ving.carePeople.length;
                    for (let i=0;i<l;i++){
                        if (ving.carePeople[i].uId===ving.oneAuthor.uId){
                            return;
                        }
                    }

                    let self=this;
                    let xmlHttp=new XMLHttpRequest();
                    xmlHttp.open('post',ving.url.addAttentionUser+'?uId_S='+ving.user.uId+'&uId_E='+ving.oneAuthor.uId);
                    xmlHttp.send();
                    xmlHttp.onreadystatechange=()=>{
                        if (xmlHttp.readyState!==4){}
                        else if (xmlHttp.status!==200){
                            self.ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                        }
                        else {
                            if (xmlHttp.responseText==='true'){
                                ving.message('您成功成为了这位大人的粉丝了呢');
                                this.moreOrCare.innerHTML='已关注';
                                ving.carePeople[ving.carePeople.length]=ving.oneAuthor;
                                ving.user.concernNumber+=1;
                            }else {
                                ving.message('发生错误了啊┭┮﹏┭┮您可能已经关注过这位大人了');
                            }
                        }
                    };
                }}>关注</span>
            });
        }
        let self=this;
        function assureAttention() {
            if (ving.user!==null&&ving.oneAuthor!==null&&ving.carePeople!==null){
                if (self.moreOrCare===null){
                    window.setTimeout(assureAttention,300);
                    return;
                }
                let l=ving.carePeople.length;
                for (let i=0;i<l;i++){
                    if (ving.carePeople[i].uId===ving.oneAuthor.uId){
                        self.moreOrCare.innerHTML='已关注';
                        return;
                    }
                }
                self.moreOrCare.innerHTML='关注';
            }
        }
        assureAttention();

        let proXml=new XMLHttpRequest();
        proXml.open('get',ving.url.personalPros+this.user.uId);
        proXml.send();
        proXml.onreadystatechange=()=>{
            if (proXml.readyState!==4){}
            else if (proXml.status!==200)
                ving.message('发生了未知错误'+proXml.status);
            else {
                if (proXml.responseText==='')
                    return;
                let pData=Array.from(JSON.parse(proXml.responseText));
                let circleType=['生活圈','口技圈','朗读圈','配音圈','ASMR'];
                pData.forEach((item)=>{
                    let one=document.createElement("div");
                    one.className='one';
                    let cover=document.createElement("img");
                    cover.className='cover';
                    cover.src=item.cover;
                    cover.alt='';
                    one.appendChild(cover);
                    let nameCircleTime=document.createElement("div");
                    nameCircleTime.className='nameCircleTime';
                    let name=document.createElement("span");
                    name.className='name';
                    name.innerHTML=item.pName;
                    name.onclick=()=>{
                        ving.setState({
                            nowPage:<PlayPage ving={ving} product={item}/>
                        });
                    };
                    nameCircleTime.appendChild(name);
                    let circle=document.createElement("span");
                    circle.className='circle';
                    circle.innerHTML=circleType[item.type];
                    nameCircleTime.appendChild(circle);
                    nameCircleTime.appendChild(document.createElement("br"));
                    let time=document.createElement("span");
                    time.className='time';
                    time.innerHTML='发布于'+item.releaseTime.slice(0,10)+'&nbsp;&nbsp;'+item.releaseTime.slice(11,19);
                    nameCircleTime.appendChild(time);
                    one.appendChild(nameCircleTime);
                    let record=document.createElement('div');
                    record.className='record';
                    let likeImg=document.createElement("img");
                    likeImg.className='like';
                    likeImg.src=like;
                    likeImg.alt='';
                    record.appendChild(likeImg);
                    record.innerHTML+='&nbsp;点赞&nbsp;&nbsp;'+item.loveNumber+'&nbsp;&nbsp;&nbsp;';
                    let commentImg=document.createElement("img");
                    commentImg.className='comment';
                    commentImg.src=comment;
                    commentImg.alt='';
                    record.appendChild(commentImg);
                    record.innerHTML+='&nbsp;评论&nbsp;&nbsp;'+item.commentsNumber+'&nbsp;&nbsp;&nbsp;';
                    let addImg=document.createElement("img");
                    addImg.className='add';
                    addImg.src=add;
                    addImg.alt='';
                    record.appendChild(addImg);
                    record.innerHTML+='&nbsp;收藏&nbsp;&nbsp;'+item.collectNumber+'&nbsp;&nbsp;&nbsp;';
                    one.appendChild(record);
                    let operation=document.createElement("div");
                    operation.className='operation';
                    let edit=document.createElement('span');
                    edit.className='edit';
                    edit.innerHTML='编辑';
                    edit.onclick=()=>{
                        if (self.moreOrCare==='care'){
                            ving.message('不要乱动别人的作品噢O(∩_∩)O');
                            return;
                        }
                        self.setState({
                            myCares:null
                        });
                        window.setTimeout(()=>{
                            self.setState({
                                myCares:
                                    <div className={'editContainer'}>
                                        <label>作品名：</label><input type={'text'} defaultValue={item.pName} className={'editName'} ref={editName=>this.editName=editName}/><br/>
                                        <label>标&emsp;签：</label><input type={'text'} defaultValue={item.label} className={'editLabel'} ref={editLabel=>this.editLabel=editLabel}/><br/>
                                        <label>简&emsp;介：</label><br/>
                                        <textarea defaultValue={item.description} className={'editDes'} ref={editDes=>this.editDes=editDes} maxLength={100}/><br/>
                                        <button className={'editFinish'} onClick={()=>{
                                            let xmlHttp=new XMLHttpRequest();
                                            xmlHttp.open('post',ving.url.setProductionInfo);
                                            let info={
                                                pId:item.pId,
                                                pName:this.editName.value,
                                                label:this.editLabel.value,
                                                description:this.editDes.value
                                            };
                                            xmlHttp.setRequestHeader('Content-Type', 'application/json');
                                            xmlHttp.send(JSON.stringify(info));
                                            xmlHttp.onreadystatechange=()=>{
                                                if (xmlHttp.readyState!==4){}
                                                else if (xmlHttp.status!==200)
                                                    ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                                                else {
                                                    if (xmlHttp.responseText==='false')
                                                        ving.message('不知道怎么回事，您的作品不听话了呢');
                                                    else {
                                                        ving.setState({
                                                            nowPage:null
                                                        });
                                                        window.setTimeout(()=>{
                                                            ving.toNext(2);
                                                        },1);
                                                    }
                                                }
                                            };
                                        }}>完成</button>
                                    </div>,
                                careLWord:'编辑作品'
                            });

                            self.myCares.style.top='0';
                        },1);
                    };
                    operation.appendChild(edit);
                    let remove=document.createElement('span');
                    remove.className='remove';
                    remove.innerHTML='删除';
                    remove.onclick=()=>{
                        ving.message('暂时不支持删除操作┭┮﹏┭┮');
                    };
                    operation.appendChild(remove);
                    let moreOp=document.createElement('span');
                    moreOp.className='moreOp';
                    moreOp.innerHTML='更多操作';
                    moreOp.onclick=()=>{
                        ving.message('略略略');
                    };
                    operation.appendChild(moreOp);
                    one.appendChild(operation);
                    let separator=document.createElement("div");
                    separator.className='separator';
                    product.appendChild(one);
                    product.appendChild(separator);
                });
            }
        };

        let collectXml=new XMLHttpRequest();
        collectXml.open('get',ving.url.personalCollects+this.user.uId);
        collectXml.send();
        collectXml.onreadystatechange=()=>{
            if (collectXml.readyState!==4){}
            else if (collectXml.status!==200)
                ving.message('发生了未知错误'+collectXml.status);
            else {
                if (collectXml.responseText==='')
                    return;
                let cData=Array.from(JSON.parse(collectXml.responseText));

                cData.forEach((item)=>{
                    let one=document.createElement("div");
                    one.className='one';
                    let cover=document.createElement("img");
                    cover.className='cover';
                    cover.src=item.cover;
                    cover.alt='';
                    one.appendChild(cover);
                    let nameCircleTime=document.createElement("div");
                    nameCircleTime.className='nameCircleTime';
                    let name=document.createElement("span");
                    name.className='name';
                    name.innerHTML=item.aName;
                    name.onclick=()=>{
                        let colProXml=new XMLHttpRequest();
                        colProXml.open('get',ving.url.findProductionsByAlbumId+item.aId);
                        colProXml.send();
                        colProXml.onreadystatechange=()=>{
                            if (colProXml.readyState!==4) {}
                            else if (colProXml.status!==200)
                                ving.message('发生了未知错误┭┮﹏┭┮'+colProXml.status);
                            else {
                                if (colProXml.responseText===''){
                                    ving.message('这个作品集里面居然空空如也！┭┮﹏┭┮');
                                    return;
                                }
                                let products=JSON.parse(colProXml.responseText);
                                let proDis=[];
                                let circleType=['生活圈','口技圈','朗读圈','配音圈','ASMR'];
                                products.forEach((item,index)=>{
                                    proDis[index]=
                                        <div className={'one colProOne'} key={index}>
                                            <img src={item.cover} className={'cover'} alt={''}/>
                                            <div className={'nameCircleTime'}>
                                                <span className={'name corProName'} onClick={()=>{
                                                    ving.setState({
                                                        nowPage:<PlayPage ving={ving} product={item}/>
                                                    });
                                                }}>{item.pName}</span>
                                                <span className={'circle colProCir'}>{circleType[item.type]}</span>
                                                <br/>
                                                <span className={'time colProTime'}>{'发布于'+item.releaseTime.slice(0,10)+'  '+item.releaseTime.slice(11,19)}</span>
                                            </div>
                                            <div className={'record colProRecord'}>
                                                <img className={'like'} src={like} alt={''}/>
                                                {' 点赞  '+item.loveNumber+'   '}
                                                <img className={'comment'} src={comment} alt={''}/>
                                                {' 评论  '+item.commentsNumber+'   '}
                                                <img className={'add'} src={add} alt={''}/>
                                                {' 收藏  '+item.collectNumber+'   '}
                                            </div>
                                            <div className={'operation colProOp'}>(/≧▽≦)/</div>
                                        </div>
                                });
                                this.setState({
                                    myCares:proDis,
                                    careLWord:'作品列表'
                                });
                                this.myCares.style.top='0';
                            }
                        };
                    };
                    nameCircleTime.appendChild(name);
                    let circle=document.createElement("span");
                    circle.className='circle';
                    circle.innerHTML='O(∩_∩)O';
                    nameCircleTime.appendChild(circle);
                    let time=document.createElement("span");
                    time.className='time';
                    time.innerHTML='发布于'+item.createdTime.slice(0,10)+'&nbsp;&nbsp;'+item.createdTime.slice(11,19);
                    nameCircleTime.appendChild(time);
                    one.appendChild(nameCircleTime);
                    let record=document.createElement('div');
                    record.className='record';
                    let addImg=document.createElement("img");
                    addImg.className='add';
                    addImg.src=add;
                    addImg.alt='';
                    record.appendChild(addImg);
                    record.innerHTML+='&nbsp;收藏&nbsp;&nbsp;'+item.collectNumber+'&nbsp;&nbsp;&nbsp;';
                    one.appendChild(record);
                    let operation=document.createElement("div");
                    operation.className='operation';
                    let edit=document.createElement('span');
                    edit.className='edit';
                    edit.innerHTML='编辑';
                    edit.onclick=()=>{
                        ving.message('暂不支持编辑操作┭┮﹏┭┮');
                    };
                    operation.appendChild(edit);
                    let remove=document.createElement('span');
                    remove.className='remove';
                    remove.innerHTML='删除';
                    remove.onclick=()=>{
                        ving.message('暂不支持删除操作┭┮﹏┭┮');
                    };
                    operation.appendChild(remove);
                    let moreOp=document.createElement('span');
                    moreOp.className='moreOp';
                    moreOp.innerHTML='更多操作';
                    moreOp.onclick=()=>{
                        ving.message('┭┮﹏┭┮');
                    };
                    operation.appendChild(moreOp);
                    one.appendChild(operation);
                    let separator=document.createElement("div");
                    separator.className='separator';
                    collect.appendChild(one);
                    collect.appendChild(separator);
                });
            }
        }
    }

    render(){
        return(
            <div id={'PCenterV2'}>
                <div className={'avatar'} style={{backgroundImage:'url('+this.state.avatar+')'}}> </div>
                <span className={'nameSign'}>
                        <span className={'userName'}>{this.state.name}</span>
                        {this.state.moreOrCare}
                        <br/>
                        <span className={'sign'}>{this.state.sign}</span>
                </span>
                <br/>
                <span className={'productLead'} ref={pLead=>this.pLead=pLead} onClick={()=>{
                    this.container.scrollLeft=0;
                    this.pLead.style.fontSize='1.3rem';
                    this.pLead.style.borderLeft= '0.1rem solid #70e8f5';
                    this.cLead.style.fontSize='1.1rem';
                    this.cLead.style.borderLeft= '';
                }}>作品</span>
                <span className={'collectLead'} ref={cLead=>this.cLead=cLead} onClick={()=>{
                    this.container.scrollLeft=this.product.scrollWidth;
                    this.pLead.style.fontSize='1.1rem';
                    this.pLead.style.borderLeft= '';
                    this.cLead.style.fontSize='1.3rem';
                    this.cLead.style.borderLeft= '0.1rem solid #70e8f5';
                }}>作品集</span>
                <br/>
                <div className={'container'} ref={container=>this.container=container}>
                    <div className={'product'} ref={product=>this.product=product}>

                    </div>

                    <div className={'collect'} ref={collect=>this.collect=collect}>

                    </div>

                    <div className={'right'}>
                        <div className={'fans'} onClick={()=>{
                            if (this.ving.moreOrCare==='care'){
                                this.ving.message('暂时不支持看别人的粉丝呢~ o(*￣▽￣*)o');
                                return;
                            }
                            let self=this;
                            let xmlHttp=new XMLHttpRequest();
                            xmlHttp.open('get',this.ving.url.findAttentionStartUserInfoByUID+this.user.uId);
                            xmlHttp.send();
                            xmlHttp.onreadystatechange=()=>{
                                if (xmlHttp.readyState!==4){}
                                else if (xmlHttp.status!==200)
                                    self.ving.message('发生了未知错误'+xmlHttp.status);
                                else {
                                    if (xmlHttp.responseText===''){
                                        self.ving.message('居然没有人关注您，真让人头大┭┮﹏┭┮');
                                        return;
                                    }
                                    let fans=JSON.parse(xmlHttp.responseText);
                                    let fansDis=[];
                                    fans.forEach((item,index)=>{
                                        fansDis[index]=
                                            <div className={'oneCare'} key={index}>
                                                <Avatar src={item.portrait} size={'large'} className={'careAvatar'}/>
                                                <span className={'careName'} onClick={()=>{
                                                    let ving=this.ving;
                                                    ving.oneAuthor=item;
                                                    ving.moreOrCare='care';
                                                    ving.setState({
                                                        nowPage:null
                                                    });
                                                    window.setTimeout(()=>{
                                                        ving.toNext(2);
                                                    },1);
                                                }}>{item.uName}</span>
                                                <span className={'careSign'}>{item.signature}</span>
                                                <span className={'cancelCare'} onClick={()=>{
                                                    this.ving.message('(/≧▽≦)/');
                                                }}>(/≧▽≦)/</span>
                                            </div>
                                    });
                                    this.setState({
                                        myCares:fansDis,
                                        careLWord:'粉丝列表'
                                    });
                                    this.myCares.style.top='0';
                                }
                            };
                        }}>
                            <div className={'first'}> </div>
                            <span className={'word'}>粉丝</span>
                            <span className={'number'}>{this.state.fans}</span>
                        </div>
                        <div ref={careDisplayArea=>this.caraDispayArea=careDisplayArea} className={'care'} onClick={()=>{
                            if (this.ving.moreOrCare==='care'){
                                this.ving.message('暂时不支持看别人关注的人呢~ o(*￣▽￣*)o');
                                return;
                            }
                            let careDis=[];
                            this.ving.carePeople.forEach((item,index)=>{
                                careDis[index]=
                                    <div className={'oneCare'} key={index}>
                                        <Avatar src={item.portrait} size={'large'} className={'careAvatar'}/>
                                        <span className={'careName'} onClick={()=>{
                                            let ving=this.ving;
                                            ving.oneAuthor=item;
                                            ving.moreOrCare='care';
                                            ving.setState({
                                                nowPage:null
                                            });
                                            window.setTimeout(()=>{
                                                ving.toNext(2);
                                            },1);
                                        }}>{item.uName}</span>
                                        <span className={'careSign'}>{item.signature}</span>
                                        <span className={'cancelCare'} onClick={()=>{
                                            let self=this;
                                            let xmlHttp=new XMLHttpRequest();
                                            xmlHttp.open('post',this.ving.url.removeAttentionUser+'?uId_S='+this.ving.user.uId+'&uId_E='+item.uId);
                                            xmlHttp.send();
                                            xmlHttp.onreadystatechange=()=>{
                                                if (xmlHttp.readyState!==4){}
                                                else if (xmlHttp.status!==200)
                                                    self.ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                                                else {
                                                    if (xmlHttp.responseText==='true'){
                                                        let tmp=this.state.myCares;
                                                        /*for (let i=0;i<tmp.length;i++){
                                                            if (tmp[i].key===index){
                                                                tmp.splice(i,1);
                                                                break;
                                                            }
                                                        }*/
                                                        tmp[index]=null;
                                                        self.setState({
                                                            myCares:null
                                                        });
                                                        window.setTimeout(()=>{
                                                            self.setState({
                                                                myCares:tmp,
                                                                care:self.state.care-1
                                                            });
                                                        },1);
                                                        self.ving.carePeople.splice(index,1);
                                                        self.ving.user.concernNumber-=1;
                                                    }
                                                    else
                                                        self.ving.message('不知道怎么回事，似乎缘分不让您离开这位大人呢');
                                                }
                                            };
                                        }}>取关</span>
                                    </div>
                            });
                            this.setState({
                                myCares:careDis,
                                careLWord:'关注列表'
                            });
                            this.myCares.style.top='0';
                        }}>
                            <div className={'first'}> </div>
                            <span className={'word'}>关注</span>
                            <span className={'number'}>{this.state.care}</span>
                        </div>
                        <div className={'proNumber'}>
                            <div className={'first'}> </div>
                            <span className={'word'}>作品</span>
                            <span className={'number'}>{this.state.proNumber}</span>
                        </div>
                        <div className={'colNumber'}>
                            <div className={'first'}> </div>
                            <span className={'word'}>作品集</span>
                            <span className={'number'}>{this.state.colNumber}</span>
                        </div>
                        <div className={'likeNumber'}>
                            <div className={'first'}> </div>
                            <span className={'word'}>总赞数</span>
                            <span className={'number'}>{this.state.likeNumber}</span>
                        </div>
                    </div>
                </div>

                <div className={'myCares'} ref={myCares=>this.myCares=myCares}>
                    <div className={'caresLead'}>
                        <span className={'careLWord'}>{this.state.careLWord}</span>
                        <span className={'closeCare'} onClick={()=>{
                            this.myCares.style.top='-90%';
                        }}>X</span>
                    </div>
                    <div className={'caresSep'}> </div>
                    {this.state.myCares}
                </div>
            </div>
        );
    }
}

export default PCenterV2;


