import React, { Component } from 'react'
import {Icon} from 'antd'
import arrowLeft from '../images/arrowleft.png'
import arrowRight from '../images/arrowright.png'
import UpLoadAndRecord from "./UpLoadAndRecord";
import PreviewBar from "./PreviewBar";

class PreviewTailor extends Component{
    constructor(props){
        super(props);
        this.src=props.src;
        this.create=props.create;
        this.state={
            previewBar:<PreviewBar className={'preview'} src={this.src}/>
        };
        this.backAudio={
            currentTime:0,
            duration:0
        };
    }

    componentDidMount(){
        this.play.style.display='none';
        this.backL=Number.parseFloat(window.getComputedStyle(this.back).width);

        let back=this.back;
        let progress=this.progress;
        let userLength=this.userLength;
        let left=this.left;
        let right=this.right;
        let audio=document.getElementById('createdAudio');
        let self=this;

        this.rightL=window.getComputedStyle(back).width-right.clientWidth;
        right.style.left=this.rightL+'px';
        this.leftL=0;
        function rLength(){
            return Number.parseFloat(window.getComputedStyle(right).left)+right.clientWidth;
        }
        function lLength(){
            return Number.parseFloat(window.getComputedStyle(left).left);
        }
        function backLength(){
            return Number.parseFloat(window.getComputedStyle(back).width);
        }
        function toTime(seconds){
            let time='';
            let minute=Math.floor(seconds/60);
            if(minute>9)
                time+=minute;
            else
                time+='0'+minute;
            time+=':';
            let second=Math.floor(seconds%60);
            if (second>9)
                time+=second;
            else
                time+='0'+second;
            return time;
        }
        audio.onloadedmetadata=function(){
            let duration=audio.duration;
            self.create.start=0;
            self.create.end=1;
            self.userEnd.innerHTML=toTime(duration);

            self.interval=window.setInterval(function () {
                let audioL=audio.currentTime/duration*backLength();
                if(audioL<lLength()||audioL>rLength()){
                    audio.currentTime=lLength()/backLength()*duration;
                    if (self.backAudio.duration!==0)
                        self.backAudio.currentTime=0;
                    self.pause.click();
                }
                progress.style.width=audio.currentTime/duration*backLength()-lLength()+'px';
                self.now.innerHTML=toTime(audio.currentTime);

                if (backLength()!==self.backL){
                    self.leftL=self.leftL/self.backL*backLength();
                    self.rightL=self.rightL/self.backL*backLength();
                    self.backL=backLength();
                    left.style.left=self.leftL+'px';
                    right.style.left=self.rightL+'px';
                    progress.style.left=self.leftL+'px';
                    userLength.style.left=self.leftL+'px';
                    userLength.style.width=changeL()+'px';
                }
            },100);
            back.onclick=function(e){
                if(e.offsetX>rLength()||e.offsetX<lLength()){
                    progress.style.width=0;
                    audio.currentTime=lLength()/backLength()*duration;
                    self.pause.click();
                }
                else{
                    progress.style.width=e.offsetX-lLength()+'px';
                    audio.currentTime=e.offsetX/backLength()*duration;
                }
            };
            self.addBack=()=>{
                back.onclick=function(e){
                    if(e.offsetX>rLength()||e.offsetX<lLength()){
                        progress.style.width=0;
                        audio.currentTime=lLength()/backLength()*duration;
                        self.backAudio.currentTime=0;
                        self.pause.click();
                    }
                    else{
                        progress.style.width=e.offsetX-lLength()+'px';
                        audio.currentTime=e.offsetX/backLength()*duration;
                        self.backAudio.currentTime=(e.offsetX-lLength())/backLength()*duration%self.backAudio.duration;
                    }
                };
            };

            function changeL(){
                return rLength()-lLength();
            }
            left.onmousedown=function (e) {
                e.preventDefault();
                let clickX=e.clientX;
                document.onmousemove=function (e) {
                    let l=lLength()+e.clientX-clickX;
                    if(l>=0&&l<Number.parseFloat(window.getComputedStyle(right).left)){
                        self.leftL=l;
                        self.create.start=l/backLength();

                        left.style.left=l+'px';
                        progress.style.left=l+'px';
                        userLength.style.left=l+'px';
                        userLength.style.width=changeL()+'px';
                        self.userStart.innerHTML=toTime(lLength()/backLength()*duration);
                        clickX+=e.clientX-clickX;
                    }
                }
            };
            document.onmouseup=function () {
                document.onmousemove=null;
            };

            right.onmousedown=function (e) {
                e.preventDefault();
                let clickX=e.clientX;
                document.onmousemove=function (e) {
                    let l=rLength()+e.clientX-clickX;
                    if(l<=backLength()&&l>lLength()+left.clientWidth){
                        l-=right.clientWidth;
                        self.rightL=l;
                        self.create.end=l/backLength();

                        right.style.left=l+'px';
                        userLength.style.width=changeL()+'px';
                        self.userEnd.innerHTML=toTime(rLength()/backLength()*duration);
                        clickX+=e.clientX-clickX;
                    }
                }
            };
            document.onmouseup=function () {
                document.onmousemove=null;
            };
        };
    }

    componentWillUnmount(){
        window.clearInterval(this.interval);
    }

    render(){
        return(
            <div className={'previewTailor'}>
                {this.state.previewBar}
                <div className={'operation'}>
                    <span ref={pause=>this.pause=pause} className={'play'} onClick={()=>{
                        this.pause.style.display='none';
                        this.play.style.display='initial';
                        document.getElementById('createdAudio').pause();
                    }}><Icon className={'pauseIcon'} type="pause" /><span className={'pause'}>暂停</span></span>
                    <span ref={play=>this.play=play} className={'play'} onClick={()=>{
                        this.play.style.display='none';
                        this.pause.style.display='initial';
                        document.getElementById('createdAudio').play();
                    }}><Icon className={'playIcon'} type="caret-right" /><span className={'pause'}>播放</span></span><br/>
                    <span className={'reRecord'} onClick={()=>{
                        this.create.setState({
                            creation:<UpLoadAndRecord create={this.create} todo={1}/>
                        });
                    }}><Icon className={'reRecordIcon'} type="sync" />录制</span><br/>
                    <span className={'upInOperation'} onClick={()=>{
                        this.create.setState({
                            creation:<UpLoadAndRecord create={this.create} todo={0}/>
                        });
                    }}><Icon className={'upIcon'} type="upload" />上传</span><br/>
                    <span className={'addBack'} onClick={()=>{
                        this.backMusic.click();
                    }}>
                        <Icon type="link" className={'addIcon'} />背景音乐
                        <input type={'file'} ref={back=>this.backMusic=back} style={{zIndex:'0', opacity:'0'}} onChange={()=>{
                            this.setState({
                                previewBar:<PreviewBar className={'preview'} back={URL.createObjectURL(this.backMusic.files[0])} container={this}/>
                            });
                        }}/>
                    </span>
                </div>
                <div ref={back=>this.back=back} className={'progressBack'}/>
                <div ref={progress=>this.progress=progress} className={'progress'}/>
                <div ref={userLength=>this.userLength=userLength} className={'userLength'}/>
                <img ref={left=>this.left=left} src={arrowLeft} className={'arrowLeft'} alt={''}/>
                <img ref={right=>this.right=right} src={arrowRight} className={'arrowRight'} alt={''}/>
                <div className={'bottomTime'}>
                    <span ref={userStart=>this.userStart=userStart} className={'userStart'}>00:00</span>
                    <span ref={now=>this.now=now} className={'now'}>1:50</span>
                    <span ref={userEnd=>this.userEnd=userEnd} className={'userEnd'}>2:30</span>
                </div>
            </div>
        );
    }
}

export default PreviewTailor;


