import React, {Component } from 'react'
import '../css/OneCircle.css'
import PlayPage from "./PlayPage";

class OneCircle extends Component{
    constructor(props){
        super();
        this.ving=props.ving;
        this.products=this.ving.someProducts[this.ving.someIndex];
        props.ving.setState({
            vingOrReturn:'< 返回',
            Ving:'微音',
            separator:'',
            first:'',
            second:'',
            third:'',
            fourth:'',
            return:()=>{
                props.ving.returnLast();
            },
            firstClick:null,
            secondClick:null,
            thirdClick:null,
            fourthClick:null
        });
        this.state={
            productContainer:[]
        };
    }

    componentDidMount() {
        let container=this.container;
        //判断浏览器
        let isIE = navigator.userAgent.match(/MSIE (\d)/i);
        isIE = isIE ? isIE[1] : undefined;
        let isFF = /FireFox/i.test(navigator.userAgent);

        if (isIE < 9) //传统浏览器使用MouseWheel事件
            container.attachEvent("onmousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格3行，每行40像素，所以除以120
                let v = e.wheelDelta / 2;
                container.scrollLeft += v;
                /*scroll.scrollLeft += v;*/
                //阻止浏览器默认方法
                return false;
            });
        else if (!isFF) //除火狐外的现代浏览器也使用MouseWheel事件
            container.addEventListener("mousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                let v = -e.wheelDelta / 2;
                container.scrollLeft += v;
                /*scroll.scrollLeft += v;*/
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        else //奇葩的火狐使用DOMMouseScroll事件
            container.addEventListener("DOMMouseScroll", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格是3行，但是要注意，这里和像素不同的是它是负值
                container.scrollLeft += e.detail * 80;
                /*scroll.scrollLeft += e.detail * 80;*/
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        this.ving.touchMove(container,1);

        let products = this.products;
        let l = products.length;
        let conNum = Math.ceil(l / 18);
        let cnt = 0;
        let pContainer = [];
        let key=10000;
        for (let i = 0; i < conNum; i++) {
            pContainer[i] = [];
            let contents = [];
            for (let j = 0; j < 3; j++) {
                let oneContent = [];
                for (let k = 0; k < 6 && cnt < l; k++) {
                    let product=products[cnt];
                    oneContent[k] =
                        <div className={'productResultContent'}
                             style={{backgroundImage: 'url(' + product.cover + ')'}} onClick={()=>{
                                 this.ving.setState({
                                     nowPage:<PlayPage ving={this.ving} product={product}/>
                                 });
                        }} key={key++}>
                            <div className={'productResultName'}>{products[cnt++].pName}</div>
                            <div className={'productResultBottom'}> </div>
                        </div>
                }
                contents[j] =
                    <span key={key++}>
                        <div className={'productResultContents'}>
                            {oneContent}
                        </div>
                        <br/>
                    </span>
            }
            pContainer[i] =<div key={key++} className={'productResultContainer'}>{contents}</div>
            this.setState({
                productContainer: pContainer
            });
        }
    }

    render(){
        return(
            <div id={'OneCircle'} className={'mainContainer'}>
                <div className={'productResult'} ref={container => this.container = container}>
                    <div className={'productResultLead'}>
                        <p>作</p>
                        <p>品</p>
                    </div>
                    {this.state.productContainer}
                </div>
            </div>
        );
    }
}

export  default OneCircle;


