import React, { Component } from 'react'
import upload from '../images/product.png'
import play from '../images/play.png'
import pause from '../images/pause.png'
import PreviewTailor from "./PreviewTailor";

class UpLoadAndRecord extends Component{
    constructor(props){
        super(props);
        this.create=props.create;

        this.todo=props.todo;
    }

    componentDidMount(){
        let create=this.create;
        let recordImg=this.recordImg;
        let record=this.record;
        let self=this;
        this.upFile.onchange=function () {
            create.music=this.files[0];
            create.setState({
                creation:<PreviewTailor src={URL.createObjectURL(this.files[0])} create={create}/>
            });
        };

        let p=navigator.mediaDevices.getUserMedia({audio:true,video:false});
        p.then(function (mediaStream) {
            let recorder=new MediaRecorder(mediaStream);
            recorder.ondataavailable=function (blob) {
                create.music=blob.data;
                create.setState({
                    creation:<PreviewTailor src={URL.createObjectURL(blob.data)} create={create}/>
                });
            };
            function toTime(seconds){
                let time='';
                let minute=Math.floor(seconds/60);
                if(minute>9)
                    time+=minute;
                else
                    time+='0'+minute;
                time+=':';
                let second=Math.floor(seconds%60);
                if (second>9)
                    time+=second;
                else
                    time+='0'+second;
                return time;
            }
            function clickRecord() {
                if (record.innerHTML==='开始录制'){
                    recorder.start();
                    recordImg.src=pause;
                    record.innerHTML='完成录制';
                    let startTime=new Date();
                    self.interval=window.setInterval(function () {
                        self.time.innerHTML=toTime(Math.floor((new Date().getTime()-startTime.getTime())/1000));
                    },500);
                    self.time.style.opacity=1;
                    self.time.style.zIndex=2;
                    self.upImg.style.opacity=0;
                    self.upWords.style.opacity=0;

                }
                else {
                    window.clearInterval(self.interval);
                    recorder.stop();
                }
            }
            record.onclick=clickRecord;
            recordImg.onclick=clickRecord;

            if(self.todo===1)
                record.click();
        });

        if(self.todo===0)
            this.upFile.click();

    }

    componentWillUnmount(){
        this.time.style.zIndex=0;
    }

    render(){
        return(
            <div className={'uploadAndRecord'}>
                <img ref={upImg=>this.upImg=upImg} src={upload} className={'uploadImg'} alt={''}/>
                <span ref={upWords=>this.upWords=upWords} className={'upload'}>上传声音文件</span>
                <input ref={upFile=>this.upFile=upFile} type={'file'} className={'upFile'}/>
                <img ref={recordImg=>this.recordImg=recordImg} src={play} className={'recordImg'} alt={''}/>
                <span ref={record=>this.record=record} className={'record'}>开始录制</span>
                <span ref={time=>this.time=time} className={'recordTime'}>00:00</span>
            </div>
        );
    }
}

export default UpLoadAndRecord;


