import React, {Component} from 'react';

class PreviewBar extends Component {
    constructor(props) {
        super(props);
        this.className = ' ' + props.className;
        this.state = {
            loop: true,
            back: ''
        };
    }

    componentDidMount() {
        let canvas = document.getElementById("canvas"),
            canvasCtx = canvas.getContext("2d"),
            canvasWidth = canvas.width,
            canvasHeight = canvas.height;
        canvas.style.width = '100%';
        let audio = this.audio;

        window.AudioContext = window.AudioContext || window.webkitAudioContext;
        if (!window.AudioContext) {
            return;
        }
        let audioCtx = new (window.AudioContext || window.webkitAudioContext)(),
            analyser = audioCtx.createAnalyser();  // 提取音频数据，你需要AnalyserNode，它可以通过createAnalyser创建
        this.analyser = analyser;
        this.audioCtx = audioCtx;

        let audioSource = audioCtx.createMediaElementSource(audio);
        audioSource.connect(analyser);
        analyser.connect(audioCtx.destination);
        analyser.fftSize = 2048;
        let barLength = analyser.frequencyBinCount,
            barHeight = new Uint8Array(barLength);

        let requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame || window.webkitRequestAnimationFrame;

        let gradient_1 = canvasCtx.createLinearGradient(canvasWidth * .5, 0, canvasWidth * .5, canvasHeight * .5);
        gradient_1.addColorStop(0.2, '#f2fcfe');
        gradient_1.addColorStop(1, '#1c92d2');
        let gradient_2 = canvasCtx.createLinearGradient(canvasWidth * .5, canvasHeight * .5, canvasWidth * .5, canvasHeight);
        let barNum = 80;
        let step = Math.floor(barHeight.length / barNum);

        gradient_2.addColorStop(0, '#FF4500');
        gradient_2.addColorStop(0.8, '#FFFFFF');

        let self = this;


        let  bandNum=8;
        let num=0;
        let upperP=[];
        let upperDF=[];
        //保存每一帧的均值
        let upperE = [];
        //将音频帧分成八个子频带
        let subBand = [];
        let flag=true;
        for(let i=0;i<bandNum;i++){
            upperP[i]=[];
            upperDF[i]=[];
        }
        Math.sum=function(arr){
            arr=Array.from(arr);
            let sum=0;
            let l=arr.length;
            for(let i=0;i<l;i++){
                if (arr[i]>0)
                    sum+=arr[i];
            }
            return sum;
        };
        Math.mean=function (arr) {
            arr=Array.from(arr);
            return Math.sum(arr)/arr.length;
        };
        Math.meanAn=function (arr) {
            arr=Array.from(arr);
            let sum=0;
            let l=arr.length;
            let start=Math.round(l/3);
            let end=Math.round(l*2/3);
            for(let i=start;i<end;i++){
                sum+=arr[i];
            }
            return sum/(end-start);
        };
        function draw() {
            analyser.getByteFrequencyData(barHeight);

            canvasCtx.fillStyle = '#343a40';
            canvasCtx.fillRect(0, 0, canvasWidth, canvasHeight);


            for (let i = 1; i < barNum; i++) {
                let height = barHeight[i * step] * 1.6;

                canvasCtx.fillStyle = gradient_1;
                canvasCtx.fillRect(i * 10 + canvasWidth * .5, canvasHeight * .5, 7, -height + 1);
                canvasCtx.fillRect(canvasWidth * .5 - (i - 1) * 10, canvasHeight * .5, 7, -height + 1);
                canvasCtx.fill();
                canvasCtx.fillStyle = gradient_2;
                canvasCtx.fillRect(i * 10 + canvasWidth * .5, canvasHeight * .5, 7, height + 1);
                canvasCtx.fillRect(canvasWidth * .5 - (i - 1) * 10, canvasHeight * .5, 7, height + 1);
                canvasCtx.fill();
            }

            self.drawTime = requestAnimationFrame(draw);


            if (audio.currentTime / audio.duration > 1/3 && audio.currentTime / audio.duration < 2/3) {
                //保存每一帧取样结果
                let samples = barHeight;
                let position = samples.length / 2;
                for (let i = 0; i < bandNum-1; i++) {
                    subBand[i]=[];
                    subBand[i] = samples.slice(position / 2, position);
                    position /= 2;
                }
                subBand[bandNum-1]=[];
                subBand[bandNum-1] = samples.slice(0, position);
                //计算这一帧的均值
                let logRes=[];
                for(let i=0;i<samples.length;i++){
                    logRes[i]=Math.log10(samples[i]);
                }
                upperE[num]=Math.mean(logRes);
                //计算子频带频谱能量，然后计算激变点
                //i,num表示第i子频带第num帧
                for (let i = 0; i < bandNum; i++) {
                    let tmp=[];
                    for(let j=0;j<subBand[i].length;j++){
                        tmp[j]=Math.abs(Math.log10(subBand[i][j]));
                    }
                    upperP[i][num] = Math.sum(tmp);
                    if (num>0)
                        upperDF[i][num] = Math.abs(upperP[i][num] - upperP[i][num - 1]);
                    else
                        upperDF[i][num] = upperP[i][num]===Infinity?0:upperP[i][num];
                }
                num++;
            }else if (flag&&audio.currentTime/audio.duration>=2/3){
                flag=false;
                //在采样结束后运行下面的代码
                //提取击点序列
                for(let i=0;i<8;i++){
                    //求每个子频带中平均峰值的一半
                    let middle=Math.mean(Array.from(upperDF[i]))/2;
                    //低于平均峰值一半的峰值置为0
                    for(let j=0;j<num;j++){
                        if (upperDF[i][j]<middle)
                            upperDF[i][j]=0;
                    }
                }
                //相邻四帧保留峰值最大的点作为最终击点
                for(let i=0;i<8;i++){
                    for(let j=0;j<num-3;j+=4){
                        let index=j;
                        let max=0;
                        //找出相邻四帧的最大峰值和其位置
                        for(let k=j;k<j+4;k++){
                            if (upperDF[i][k]>max) {
                                index=k;
                                max=upperDF[i][k];
                            }
                        }
                        //非最大峰值都赋值为0
                        for(let k=j;k<j+4;k++){
                            if (k!==index) {
                                upperDF[i][k]=0;
                            }
                        }
                    }
                }
                //计算每一帧的Dresult,即每一帧有多少个子频带有击点
                let dResult=[];
                for(let i=0;i<num;i++){
                    dResult[i]=0;
                    for(let j=0;j<8;j++){
                        if (upperDF[j][i]>0) {
                            dResult[i]++;
                        }
                    }
                }
                //阈值α为4
                let alpha=4;
                //记录击点
                let jidian=[];
                let numJ=0;
                //如果每一帧中击点数大于等于阈值，认为有击点
                for(let i=0;i<num;i++){
                    if (dResult[i]>=alpha) {
                        jidian[numJ++]=dResult[i];
                    }
                }
                //采用最大公约数法计算节奏击点序列的周期，即节奏速度
                let upperF=0;
                let result=Infinity;
                console.log(jidian);
                console.log(dResult);
                for(let k=0;k<numJ;k++){
                    let sum=0;
                    for(let i=0;i<numJ;i++){
                        sum+=Math.abs(jidian[i]/jidian[k]
                            -Math.round(jidian[i]/jidian[k]));
                    }
                    if (sum<result) {
                        result=sum;
                        upperF=jidian[k];
                    }
                }
                //通过每一帧声音强度的均值得到最终声音强度
                let upperAE=Math.mean(upperE);

                let emotion=["激情","热情","抒情","忧伤","快乐","轻盈","柔和","平静"];
                let core=[[200,1500],[135,1400],[140,1550],[75,1550],
                    [245,500],[165,450],[155,490],[120,370]];
                for(let i=0;i<8;i++){
                    core[i][0]=Math.log10(core[i][0])/1.4;
                    core[i][1]=Math.log(core[i][1])/1.22;
                }
                let distance=Infinity;
                let position;
                for(let i=0;i<8;i++){
                    if ((upperF-core[i][0])*(upperF-core[i][0])+(upperAE-core[i][1])*(upperAE-core[i][1])<distance) {
                        distance=(upperF-core[i][0])*(upperF-core[i][0])+(upperAE-core[i][1])*(upperAE-core[i][1]);
                        position=i;
                    }
                }
                position=Number.find(audio.duration,position);
                console.log(upperAE);
                console.log(upperF);
                console.log(emotion[position]);
            }
        }

        draw();
        let core=[[200,1500],[135,1400],[140,1550],[75,1550],
            [245,500],[165,450],[155,490],[120,370]];
        for(let i=0;i<8;i++){
            core[i][0]=Math.log10(core[i][0])/1.49;
            core[i][1]=Math.log(core[i][1])/1.22;
        }
        console.log(core);

    }

    componentWillReceiveProps(props) {
        this.setState({
            back: props.back,
        });
        this.audio.currentTime = 0;

        let backSource = this.audioCtx.createMediaElementSource(this.back);
        backSource.connect(this.analyser);
        props.container.backAudio = this.back;

        let self = this;
        this.audio.onpause = () => {
            if (self.back !== null)
                self.back.pause();
        };
        this.audio.onplay = () => {
            self.back.play();
        };
        this.audio.onended = () => {
            props.container.pause.click();
        };
        props.container.addBack();
    }

    componentWillUnmount() {
        cancelAnimationFrame(this.drawTime);
        console.log('unMount');
    }


    render() {
        return (
            <div className={'bar' + this.className}>
                <canvas id="canvas" width="1536" height="728"/>
                <audio ref={audio => this.audio = audio} src={this.props.src} autoPlay={true} id={'createdAudio'}/>
                <audio ref={audio => this.back = audio} src={this.state.back} autoPlay={true} loop={true}/>
            </div>
        );
    }
}

export default PreviewBar;


