import React, {Component} from 'react'
import single from '../images/helloSingleDog.png'
import add from '../images/add.png'
import like from '../images/like.png'
import play from '../images/play.png'
import avatar from '../images/avatar.jpg'
import {Avatar, Icon} from 'antd'
import '../css/PlayPage.css'
import Bar from "../components/Bar";
import pause from '../images/pause.png'
import unlike from '../images/unlike.png'
import unadd from '../images/unadd.png'
import Star from "../components/Star";
import Snowflake from "../components/Snowflake";
import SnowflakeColors from "../components/SnowflakeColors";
import Ripple from "../components/Ripple";
import Flower from "../components/Flower";
import Space from "../components/Space";

class PlayPage extends Component{
    constructor(props){
        super(props);
        this.rate='1.0X';
        this.ving=props.ving;
        this.ving.playPage=this;
        this.product=props.product!==undefined?props.product:{
            location:'',
            pName:'',
            description:'狼心制作，童叟无欺，文艺的人的作品，个人认为还算好听'
        };
        // this.ving.setCookie('product',this.product.pId,1);
        // this.ving.setCookie('isP','1',1);
        this.circle=['生活圈','口技圈','朗读圈','配音圈','ASMR'];

        this.eIndex=0;
        this.eL=7;

        this.commentLikeImg=[];
        this.commentContentIn=[];

        this.effects=[<Bar container={this} className={'voicePicture'}/>,<Star container={this} className={'voicePicture'}/>, <Snowflake container={this}/>,
        <SnowflakeColors container={this}/>, <Ripple container={this}/>,<Flower container={this}/>,<Space container={this}/>];
        this.state={
            description:this.product.description,
            comments:[],
            desMode:{
                height:'initial'
            },
            isSpin:false,
            number:0,

            tip:null,
            nowEffect:this.effects[(this.eIndex++)%this.eL],

            uName:this.ving.user===null?'未登录':this.ving.user.uName,
            avatar:this.ving.user===null?single:this.ving.user.portrait,

            collectDis:[]
        };

        this.audioCtx=null;
        this.analyser=null;
        this.audioSource=null;
        this.distortion=null;
        this.initCtx=()=>{
            window.AudioContext = window.AudioContext || window.webkitAudioContext;
            if (!window.AudioContext) {
                this.ving.message('浏览器不支持交互音乐');
                return false;
            }
            this.audioCtx= new (window.AudioContext || window.webkitAudioContext)();
            this.analyser = this.audioCtx.createAnalyser();  // 提取音频数据，你需要AnalyserNode，它可以通过createAnalyser创建
            this.distortion = this.audioCtx.createWaveShaper();

            this.audioSource = this.audioCtx.createMediaElementSource(document.getElementById('audio'));
            this.audioSource.connect(this.analyser);
            this.analyser.connect(this.audioCtx.destination);

            return true;
        };

        this.biquadFilter=null;
        this.convolver=null;
        this.gainNode=null;
        this.panner=null;
        this.type=-1;
        this.addEffect=(type)=>{
            if (this.biquadFilter===null){
                this.biquadFilter = this.audioCtx.createBiquadFilter();
                this.convolver = this.audioCtx.createConvolver();
                this.gainNode = this.audioCtx.createGain();
                this.panner=this.audioCtx.createPanner();
            }
            let ving=this.ving;
            if(type>-1&&type<4){
                this.analyser.disconnect();
                this.analyser.connect(this.biquadFilter);
                this.biquadFilter.connect(this.audioCtx.destination);

                this.biquadFilter.Q.value=30;
                this.biquadFilter.gain.value=20;
                switch (type){
                    case 0:
                        ving.message('高音消除');
                        this.biquadFilter.type = "lowpass";
                        this.biquadFilter.frequency.value=800;
                        break;
                    case 1:
                        ving.message('低音消除');
                        this.biquadFilter.type='highpass';
                        this.biquadFilter.frequency.value=600;
                        break;
                    case 2:
                        ving.message('低音增强');
                        this.biquadFilter.type='lowshelf';
                        this.biquadFilter.frequency.value=800;
                        break;
                    case 3:
                        ving.message('高音增强');
                        this.biquadFilter.type='highshelf';
                        this.biquadFilter.frequency.value=600;
                        break;
                    default:
                }
            }else if (type===4) {
                function makeDistortionCurve(amount) {
                    let k = typeof amount === 'number' ? amount : 50,
                        n_samples = 44100,
                        curve = new Float32Array(n_samples),
                        deg = Math.PI / 180,
                        i = 0,
                        x;
                    for ( ; i < n_samples; ++i ) {
                        x = i * 2 / n_samples - 1;
                        curve[i] = ( 3 + k ) * x * 20 * deg / ( Math.PI + k * Math.abs(x) );
                    }
                    return curve;
                }

                ving.message('老录音机效果');
                this.distortion.curve = makeDistortionCurve(100);
                this.distortion.oversample = '1x';
                this.analyser.disconnect();
                this.analyser.connect(this.distortion);
                this.distortion.connect(this.audioCtx.destination);
            }else if (type===5){
                ving.message('3D环绕效果');
                //模拟混响样本
                let length=14100;
                let buffer=this.audioCtx.createBuffer(2,length,length);
                let data=[buffer.getChannelData(0),buffer.getChannelData(1)];
                for(let i=0;i<length;i++){
                    //平方根衰减
                    let v=1-i/length;
                    //叠加12个不同频率
                    for(let j=1;j<=18;j++)v*=Math.sin(i/j);
                    //记录数据
                    data[0][i]=data[1][i]=v;
                }
                this.convolver.buffer=buffer;
                this.analyser.disconnect();
                this.analyser.connect(this.convolver);
                this.convolver.connect(this.audioCtx.destination);
            } else if (type===6){
                ving.message('3D旋转效果');
                this.analyser.disconnect();
                this.analyser.connect(this.panner);
                this.panner.connect(this.audioCtx.destination);
                let audioCtx=this.audioCtx;
                let xyz=[0,2,0];
                let aim=1;
                let r=2;
                let r2=4;
                function rotate(){
                    if (xyz[0]<r&&aim===1){
                        xyz[1]=Math.sqrt(r2-xyz[0]*xyz[0]);
                        xyz[0]+=0.1;
                    } else if (xyz[0]>=r&&aim===1){
                        aim=2;
                        xyz[0]-=0.1;
                        xyz[1]=0;
                    } else if (xyz[0]>-r&&aim===2){
                        xyz[1]=-Math.sqrt(r2-xyz[0]*xyz[0]);
                        xyz[0]-=0.1;
                    } else if (xyz[0]<=-r&&aim===2){
                        aim=1;
                        xyz[0]+=0.1;
                        xyz[1]=0;
                    }
                    audioCtx.listener.setPosition(xyz[0],xyz[1],xyz[2]);
                    if (type===6)
                        window.setTimeout(rotate,50);
                    else {
                        audioCtx.listener.setPosition(0,0,0);
                    }
                }
                rotate();
            }else {
                ving.message('普通');
                this.analyser.disconnect();
                this.analyser.connect(this.audioCtx.destination);
            }
            console.log(type);
            //this.gainNode.connect(this.audioCtx.destination);
        };

        this.tipNumber=0;

        this.width=1000;
        this.height=728;
    }

    componentDidMount(){
        let comments=this.comments;
        let ving=this.ving;
        ving.setState({
            vingOrReturn:'微音',
            Ving:'Ving',
            separator:'',
            first:'',
            second:'',
            third:'',
            fourth:'',
            return:()=>{
                ving.returnMain();
            },
            firstClick:null,
            secondClick:null,
            thirdClick:null,
            fourthClick:null,

            comments:[]
        });
        ving.navigate.style.left='2%';
        ving.navigate.style.zIndex='1';



        let xmlHttp=new XMLHttpRequest();
        xmlHttp.open('get',this.ving.url.getComment+this.product.pId,true);
        //xmlHttp.send();
        xmlHttp.onreadystatechange=()=>{
            if(xmlHttp.status!==200||xmlHttp.readyState!==4)
                return;
            let data=JSON.parse(xmlHttp.responseText);
            self.setState({
                number:data.length
            });
            let stateData=[];
            data.forEach((item,index)=>{
                stateData[index]=
                    <span className={'comment'} key={index}>
                            <Avatar src={item.commentatorPortrait} className={'commentatorAvatar'}/>{item.commentatorName}
                        <span className={'commentContent'}>{item.cText}</span>
                            <span className={'timeAndLikes'}>
                                <span>{item.commentDate.slice(0,10)}</span>
                                <img ref={img=>this.commentLikeImg[index]=img} src={unlike} alt={''} className={'showLike'} onClick={()=>{
                                    if (ving.user===null){
                                        ving.message('请先行登陆哦O(∩_∩)O');
                                        return;
                                    }
                                    if (this.commentLikeImg[index].src===like){
                                        ving.message('您已经赞过了哦');
                                        return;
                                    }
                                    let xmlHttp=new XMLHttpRequest();
                                    xmlHttp.open('post',ving.url.addCommentLoveNumber+'?cId='+item.cId);
                                    xmlHttp.send();
                                    xmlHttp.onreadystatechange=()=>{
                                        if (xmlHttp.readyState!==4){}
                                        else if (xmlHttp.status!==200)
                                            ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                                        else {
                                            if (xmlHttp.responseText==='true'){
                                                ving.message('看来您很喜欢这位客官的评论嘛O(∩_∩)O');
                                                self.commentLikeImg[index].src=like;
                                                this.commentContentIn[index].innerHTML=Number.parseInt(this.commentContentIn[index].innerHTML,10)+1+'';
                                            }else
                                                ving.message('不知道怎么回事，您给这位客官点的赞自己逃跑了┭┮﹏┭┮');
                                        }
                                    };
                                }}/>
                                <span className={'totalLikes'} ref={num=>this.commentContentIn[index]=num}>{item.loveNumber}</span>
                            </span>
                            <div className={'separateBar'}> </div>
                        </span>
            });
            self.setState({
                comments:stateData
            });
        };


        let isIE = navigator.userAgent.match(/MSIE (\d)/i);
        isIE = isIE ? isIE[1] : undefined;
        let isFF = /FireFox/i.test(navigator.userAgent);
        if (isIE < 9) //传统浏览器使用MouseWheel事件
            comments.attachEvent("onmousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格3行，每行40像素，所以除以120
                comments.scrollTop += e.wheelDelta / 4;
                //阻止浏览器默认方法
                return false;
            });
        else if (!isFF) //除火狐外的现代浏览器也使用MouseWheel事件
            comments.addEventListener("mousewheel", function (e) {
                //计算鼠标滚轮滚动的距离
                comments.scrollTop += -e.wheelDelta / 4;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        else //奇葩的火狐使用DOMMouseScroll事件
            comments.addEventListener("DOMMouseScroll", function (e) {
                //计算鼠标滚轮滚动的距离
                //一格是3行，但是要注意，这里和像素不同的是它是负值
                comments.scrollTop += e.detail * 80;
                //阻止浏览器默认方法
                e.preventDefault();
            }, false);
        ving.touchMove(comments);


        let audio=this.audio;
        let back=this.back;
        let progress=this.progress;
        let schedule=this.schedule;
        let totalLength=this.totalLength;
        let length;
        function toTime(seconds){
            let time='';
            let minute=Math.floor(seconds/60);
            if(minute>9)
                time+=minute;
            else
                time+='0'+minute;
            time+=':';
            let second=Math.floor(seconds%60);
            if (second>9)
                time+=second;
            else
                time+='0'+second;
            return time;
        }
        let self=this;
        audio.onloadedmetadata =function () {
            length=audio.duration;
            totalLength.innerHTML=toTime(length);
            self.audioSchedule=window.setInterval(function () {
                schedule.innerHTML=toTime(audio.currentTime);
                progress.style.width=audio.currentTime/length*Number.parseFloat(window.getComputedStyle(back).width)+'px';
            },1000);

            function fastForward(e) {
                progress.style.width=e.offsetX+'px';
                audio.currentTime=e.offsetX/Number.parseFloat(window.getComputedStyle(back).width)*length;
            }
            back.onclick=fastForward;
            progress.onclick=fastForward;
        };


        let cnt=0;
        /*this.waitTime=window.setInterval(()=>{
            if (navigator.onLine===false){
                cnt++;
                if (cnt===5){
                    self.setState({
                        tip:
                            <span className={'waitTip'}>
                                ┭┮﹏┭┮我已经很努力地加载了&emsp;
                                <a onClick={()=>{
                                    audio.src=self.product.location;
                                    document.getElementById('canvas').style.opacity=1;
                                    self.setState({
                                        tip:null
                                    });
                                }}>重新连接</a>
                            </span>
                    });
                    document.getElementById('canvas').style.opacity=0;
                }
            }
            else {
                if (isNaN(audio.duration)){
                    audio.src=self.product.location;
                }
                window.clearInterval(self.waitTime);
            }
        },1000);*/



        if (!this.tipNumber){
            this.ving.message('进入节奏时，试试点击进度条右下方罗盘状图标，食用效果更佳噢~ o(*￣▽￣*)o',5000);
            this.tipNumber++;
        }
    }

    componentWillUnmount(){
        window.clearInterval(this.audioSchedule);
        window.clearInterval(this.waitTime);
        this.ving.playPage=null;
    }

    render(){
        return(
            <div className={'mainContainer'} id={'playPage'}>
                {/*{this.state.tip}*/}
                <div className={'coreDisplay'}>
                    {this.state.nowEffect}
                    <audio src={this.product.location} autoPlay={true} loop={true} crossOrigin={''} id={'audio'} ref={audio=>{
                        this.audio=audio;
                    }}/>
                    <br/><br/>
                    <span className={'productInfo'}>
                        <span className={'productName'}>
                        {this.product.pName}
                        </span>
                        <span className={'productCircle'}>{this.circle[this.product.type]}</span>
                        <div className={'authorName'} onClick={()=>{
                            this.ving.message('本来是能进入个人主页的，可没有数据进不去了┭┮﹏┭┮');
                            return;

                            let ving=this.ving;
                            let infoXML = new XMLHttpRequest();
                            infoXML.open('get', ving.url.getUser + this.product.authorId);
                            infoXML.send();
                            infoXML.onreadystatechange = () => {
                                if (infoXML.readyState !== 4) {
                                }
                                else if (infoXML.status !== 200)
                                    ving.message('发生了未知错误┭┮﹏┭┮');
                                else {
                                    ving.oneAuthor = JSON.parse(infoXML.responseText);
                                    ving.moreOrCare='care';
                                    ving.toNext(2);
                                }
                            };
                        }}>
                            {this.product.authorName}
                            &nbsp;&nbsp;|&nbsp;&nbsp;
                            <span className={''}>{this.product.label}</span>
                        </div>
                    </span>
                </div>
                <div ref={back=>this.back=back} className={'progressBack'}/>
                <div ref={progress=>this.progress=progress} className={'progress'}/>
                <div className={'productTime'}>
                    <span ref={schedule=>this.schedule=schedule}>00:00</span>
                    <span ref={totalLength=>this.totalLength=totalLength} className={'totalLength'}>loading...</span>
                </div>

                <div className={'myCollect'} ref={myCollect=>this.myCollect=myCollect}>
                    <div className={'collectLead'}>
                        <span className={'collectLWord'}>作品集</span>
                        <span className={'closeCollect'} onClick={()=>{
                            this.myCollect.style.top='-80%';
                        }}>X</span>
                    </div>
                    <div className={'collectSep'}> </div>
                    {this.state.collectDis}
                </div>

                <div className={'actionIcon'}>
                    <div className={'audienceActionFirst'} onMouseOver={()=>{
                        let rates=['0.5X','1.0X','1.5X','2.0X'];
                        this.select.innerHTML='';
                        for (let i=0;i<4;i++){
                            if (rates[i]===this.rate)
                                continue;
                            let span=document.createElement("span");
                            let self=this;
                            span.style.cursor='pointer';
                            span.innerHTML=rates[i];
                            span.onclick=function () {
                                self.audio.playbackRate=Number.parseFloat(rates[i]);
                                self.select.previousSibling.innerHTML=span.innerHTML;
                                self.select.style.display='none';
                                self.rate=rates[i];
                            };
                            this.select.appendChild(span);
                            this.select.appendChild(document.createElement("br"));
                        }
                        this.select.style.display='block';
                    }} onMouseOut={()=>{
                        this.select.style.display='none';
                    }}>1.0X</div>
                    <div ref={select=>this.select=select} className={'rateSelect'} onMouseOver={()=>{
                        this.select.style.display='block';
                    }} onMouseOut={()=>{
                        this.select.style.display='none';
                    }}>
                        <span style={{cursor:'pointer'}}>0.5X</span><br/>
                        <span style={{cursor:'pointer'}}>1.5X</span><br/>
                        <span style={{cursor:'pointer'}}>2.0X</span>
                    </div>
                    <img src={unlike} alt={''} className={'audienceAction'} ref={likeAct=>this.likeAct=likeAct} onClick={()=>{
                        this.ving.message('看样子您很喜欢这部作品呢(￣▽￣)"');
                        this.likeAct.src=like;
                        return;

                        let ving=this.ving;
                        if (ving.user===null){
                            ving.message('请先行登录哦O(∩_∩)O');
                            return;
                        }
                        if (this.likeAct.src===like){
                            ving.message('您已经赞过了~ o(*￣▽￣*)o');
                            return;
                        }
                        let self=this;
                        let xmlHttp=new XMLHttpRequest();
                        xmlHttp.open('post',ving.url.updateLoveNumber+'?pId='+this.product.pId);
                        xmlHttp.send();
                        xmlHttp.onreadystatechange=()=>{
                            if (xmlHttp.readyState!==4){}
                            else if (xmlHttp.status!==200)
                                ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                            else {
                                if (xmlHttp.responseText==='true'){
                                    ving.message('看样子您很喜欢这部作品呢(￣▽￣)"');
                                    self.likeAct.src=like;
                                }else
                                    ving.message('不知道怎么回事，您给这部作品的赞自己跑掉了┭┮﹏┭┮');
                            }
                        };
                    }}/>
                    {/*<img src={last} alt={''} className={'audienceAction'} onClick={()=>{
                        this.type=(this.type-1+8)%8;
                        this.addEffect(this.type);
                    }}/>*/}
                    <Icon type="verticle-right" className={'changeAEffect'} onClick={()=>{
                        this.type=(this.type-1+8)%8;
                        this.addEffect(this.type);
                    }}  />
                    <img ref={audiencePlay=>this.audiencePlay=audiencePlay} src={pause} alt={''} className={'audiencePlay'} onClick={()=>{
                        let audio=this.audio;
                        if(audio.paused){
                            audio.play();
                            this.audiencePlay.src=pause;
                        }
                        else{
                            audio.pause();
                            this.audiencePlay.src=play;
                        }
                    }}/>
                    {/*<img src={next} alt={''} className={'audienceAction'} onClick={()=>{
                        this.type=(this.type+1)%8;
                        this.addEffect(this.type);
                    }}/>*/}
                    <Icon type="verticle-left" className={'changeAEffect'} onClick={()=>{
                        this.type=(this.type+1)%8;
                        this.addEffect(this.type);
                    }}  />
                    <img src={unadd} alt={''} className={'audienceAction'} ref={add=>this.add=add} onClick={()=>{
                        this.ving.message('略略略');
                        return;

                        if (this.add.src!==add){
                            let ving=this.ving;
                            let collect=ving.collects;
                            let collectDis=[];
                            collect.forEach((item,index)=>{
                                collectDis[index]=
                                    <span key={item.aId}>
                                    <div className={'oneCollect'}>
                                        <img className={'coverCollect'} src={item.aCover} alt={''}/>
                                        <div className={'nameCircleTime'}>
                                            <span className={'nameCollect'}>{item.aName}</span>
                                            <span className={'circleCollect'}>{item.type}</span>
                                        </div>
                                        <div className={'recordCollect'}>
                                            <img className={'addCollect '} src={add} alt={''}/>&nbsp;收藏&nbsp;&nbsp;{item.addNumber}
                                        </div>
                                        <div className={'operationCollect'} onClick={()=>{
                                            ving.message('添加成功');
                                            this.myCollect.style.top='-80%';
                                            this.add.src=add;
                                        }}>
                                            <span className={'editCollect'}>添加</span>
                                        </div>
                                    </div>
                                    <div className={'separatorCollect'}> </div>
                                </span>
                            });
                            this.setState({
                                collectDis:collectDis
                            });
                            this.myCollect.style.top='10%';
                        }
                    }}/>
                    <Icon type="compass" className={'audienceAction switch'} spin={this.state.isSpin} onClick={()=>{
                        this.setState({
                            isSpin:true
                        });
                        let self=this;
                        window.setTimeout(()=>{
                            self.setState({
                                isSpin:false
                            });
                        },1000);
                        this.setState({
                            nowEffect:this.effects[(this.eIndex++)%this.eL]
                        });
                    }} />
                </div>



                <div className={'commentDepartment'} ref={comments=>this.comments=comments}>
                    <span className={'descriptionLead'} onClick={()=>{
                        this.setState({
                            desMode:{
                                height:this.state.desMode.height==='0'?'initial':'0'
                            }
                        });
                    }}>作品简介</span>
                    <br/>
                    <span style={{...this.state.desMode}} className={'description'}>&emsp;&emsp;{this.state.description}</span>
                    <br/>
                    <span className={'totalComments'}>评论(1条)</span>
                    <span className={'comment'}>
                            <Avatar src={avatar} className={'commentatorAvatar'}/>大兔子
                        <span className={'commentContent'}>真好听啊</span>
                            <span className={'timeAndLikes'}>
                                <span>2018-7-25</span>
                                <img ref={img=>this.commentLikeImgStatic1=img} src={unlike} alt={''} className={'showLike'} onClick={()=>{
                                    this.commentLikeImgStatic1.src=like;
                                }}/>
                                <span className={'totalLikes'}>12</span>
                            </span>
                            <div className={'separateBar'}> </div>
                        </span>
                    <span className={'comment'}>
                            <Avatar src={avatar} className={'commentatorAvatar'}/>大兔子
                        <span className={'commentContent'}>真好听啊</span>
                            <span className={'timeAndLikes'}>
                                <span>2018-7-25</span>
                                <img ref={img=>this.commentLikeImgStatic2=img} src={unlike} alt={''} className={'showLike'} onClick={()=>{
                                    this.commentLikeImgStatic2.src=like;
                                }}/>
                                <span className={'totalLikes'}>12</span>
                            </span>
                            <div className={'separateBar'}> </div>
                        </span>
                </div>
                <div className={'doComment'}>
                    <Avatar src={this.state.avatar} className={'loginAvatar'}/>{this.state.uName}<br/>
                    <input ref={'userComment'} className={'typeArea'} type={'text'} placeholder={'请发表你的评论...'}/>
                    <Icon type="arrow-right" className={'submit'} onClick={()=>{
                        this.ving.message('没数据啦，发不了评论呀┭┮﹏┭┮');
                        return;

                        if (this.ving.user===null){
                            this.ving.message('请先行登录噢~ o(*￣▽￣*)o');
                        }
                        let comment={
                            commentatorId:this.ving.user.uId,
                            pId:this.product.pId,
                            cText:this.refs.userComment.value
                        };
                        this.refs.userComment.value='';
                        let xmlHttp=new XMLHttpRequest();
                        xmlHttp.open('post',this.ving.url.addComment);
                        xmlHttp.setRequestHeader('Content-Type', 'application/json');
                        xmlHttp.send(JSON.stringify(comment));
                        let self=this;
                        xmlHttp.onreadystatechange=()=>{
                            if (xmlHttp.readyState!==4){}
                            else if (xmlHttp.status!==200)
                                self.ving.message('发生了未知错误┭┮﹏┭┮'+xmlHttp.status);
                            else {
                                if (xmlHttp.responseText==='false')
                                    self.ving.message('不知道怎么回事，您的评论飞走了呢┭┮﹏┭┮');
                                else {
                                    self.componentDidMount();
                                }
                            }
                        }
                    }} />
                </div>
            </div>
        );
    }
}

export default PlayPage;


